import React, { Component } from "react";
import { Table, Checkbox } from "antd";
import {
  _failColorRed,
  _space,
  _colon,
  _vpFullDispStr,
  _propertyDispStr,
  isNotUndefinedAndNull,
  getVpTableScrollY,
  getDashForNullOrSame
} from "./Constants";
import i18n from "../i18n";

const { Column } = Table;

class PropertyVp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showPropVpDiffOnly: false
    };
  }
  render() {
    return this.getPropertyVpTable(this.props.clickedStepVpContent);
  }

  // Gets Property Vp table
  getPropertyVpTable = vpFileContent => {
    let isVpPass = true;
    let temp = vpFileContent;
    let data = [];
    isNotUndefinedAndNull(temp) &&
      isNotUndefinedAndNull(temp.diff) &&
      temp.diff.length > 0 &&
      temp.diff.forEach(dif => {
        let item = {
          key: undefined,
          expected: undefined,
          actual: undefined,
          children: undefined
        };
        if (this.state.showPropVpDiffOnly === false) {
          if (dif.match === true) {
            item.property = getDashForNullOrSame(dif.name);
            item.expected = getDashForNullOrSame(dif.expected);
            item.actual = getDashForNullOrSame(dif.actual);
            if (isNotUndefinedAndNull(dif.children)) {
              item.children = [];
              dif.children.forEach(child => {
                let itemChild = {
                  expected: undefined,
                  actual: undefined,
                  children: undefined,
                  key: undefined
                };
                itemChild.property = getDashForNullOrSame(child.name);
                itemChild.expected = getDashForNullOrSame(child.expected);
                itemChild.actual = getDashForNullOrSame(child.actual);

                itemChild.key =
                  itemChild.actual + itemChild.expected + itemChild.property;
                item.children.push(itemChild);
              });
            }
          }
        }
        if (dif.match === false) {
          isVpPass = false;
          item.property = getDashForNullOrSame(dif.name);
          item.expected = getDashForNullOrSame(dif.expected);
          item.actual = (
            <span>
              <span style={{ color: _failColorRed }}>
                {getDashForNullOrSame(dif.actual)}
              </span>
            </span>
          );
          if (isNotUndefinedAndNull(dif.children)) {
            item.children = [];
            dif.children.forEach(child => {
              let itemChild = {
                expected: undefined,
                actual: undefined,
                children: undefined,
                key: undefined
              };
              itemChild.property = getDashForNullOrSame(child.name);
              itemChild.expected = getDashForNullOrSame(child.expected);
              itemChild.actual = (
                <span>
                  <span
                    style={{ color: child.match !== true && _failColorRed }}
                  >
                    {getDashForNullOrSame(child.actual)}
                  </span>
                </span>
              );
              itemChild.key =
                itemChild.actual + itemChild.expected + itemChild.property;
              item.children.push(itemChild);
            });
          }
        }
        // adding to data array under this condition to not have empty row when "show diff" filter is ON
        if (isNotUndefinedAndNull(item.expected)) {
          item.key = item.actual + item.expected + item.property;
          data.push(item);
        }
      });

    let vptypeDispStr =
      _propertyDispStr + _space + _vpFullDispStr + _colon + _space;
    let vpResultDispStr = isVpPass
      ? i18n.t("_vpDataAsExpDispStr")
      : i18n.t("_vpDataNotAsExpDispStr");

    return (
      <React.Fragment>
        <div style={{ marginLeft: "15px", marginBottom: "5px" }}>
          <span style={{ fontWeight: "bold" }}>{vptypeDispStr}</span>
          <span>{vpResultDispStr}</span>
          <span style={{ float: "right" }}>
            <Checkbox
              onChange={this.onChangePropVpShowDiff}
              checked={this.state.showPropVpDiffOnly}
              disabled={isVpPass}
            >
              {i18n.t("_showOnlyDiffDispStr")}
            </Checkbox>
          </span>
        </div>
        <Table
          style={{
            marginLeft: "15px"
          }}
          dataSource={data}
          size="small"
          bordered
          scroll={{ y: getVpTableScrollY() }}
          pagination={false}
          className="ant-table-thead antd-table-row-hover-unset"
        >
          {
            <Column
              className="details-table-row"
              title={_propertyDispStr}
              dataIndex="property"
              key="propertyPropertyVP"
              // width={210}
              sorter={(a, b) => this.compareByAlph(a.property, b.property)}
              sortDirections={["descend", "ascend"]}
            />
          }
          <Column
            className="details-table-row"
            title={
              isVpPass
                ? i18n.t("_expectedDispStr") +
                  _space +
                  i18n.t("_andDispStr") +
                  _space +
                  i18n.t("_actualDispStr")
                : i18n.t("_expectedDispStr")
            }
            dataIndex="expected"
            key="expectedPropertyVP"
          />
          {isVpPass === false && (
            <Column
              className="details-table-row"
              title={i18n.t("_actualDispStr")}
              dataIndex="actual"
              key="actualPropertyVP"
            />
          )}
        </Table>
      </React.Fragment>
    );
  };

  // This is handler for property vp checkbox tp show diff
  onChangePropVpShowDiff = e => {
    this.setState({ showPropVpDiffOnly: e.target.checked });
  };

  // This method is used to sort table column alphbatically
  compareByAlph(b, a) {
    if (a > b) {
      return -1;
    }
    if (a < b) {
      return 1;
    }
    return 0;
  }
}

export default PropertyVp;
