import React, { Component } from "react";
import {
  productName,
  _filterNone,
  _filterNoImg,
  _filterNoSteps,
  _htmlExportType,
  _pdfExportType,
  _exportModeYes,
  _htmlExportTypeDisp,
  _pdfExportTypeDisp,
  _xmlExportType,
  _xmlExportTypeDisp
} from "./Constants";
import { Tooltip, Button, Popover, Select, Checkbox } from "antd";
import OutsideClickHandler from "react-outside-click-handler";
import {
  LoadingOutlined,
  DownloadOutlined,
  EllipsisOutlined,
  InfoCircleOutlined
  // CaretDownOutlined
} from "@ant-design/icons";
import i18n from "../i18n";

const { Option } = Select;
const ButtonGroup = Button.Group;

class Navbar extends Component {
  constructor(props) {
    super(props);

    this.escFunction = this.escFunction.bind(this);

    this.state = {
      expOptionPopoverVisible: false,
      // bydefault true means backend cli option noSteps=false
      expFilterIncStepsState: true,
      // bydefault true means backend cli option noImg=false
      expFilterIncScreenshotsState: true,
      // used to disable the checkbox for IncludeSteps
      expFilterIncStepsDisabledState: false,
      // used to disable the checkbox for IncludeImage as it is dependent on IncludeSteps
      expFilterIncScreenshotsDisabledState: false,
      // default slected exp option in dropdown is "html"
      selectedExportOptionState: _htmlExportType
    };
  }

  // Added IDs for some the component. This is required to check a specific scenario
  // i.e. we use popover for the export options and need to detect a click outside it
  // which is managed fine, but a dropdown component is present as part of this popover
  // and any click to this dropdown is detected as a click outside the popover because
  // the dropdown options pane is a separate div, so to ensure that click on this
  // dropdown is not registered as a click outside the export options popover, we
  // need to know the IDs of the options inside this dropdown.
  static compIdsToStopPropogtationFor = [
    "htmlOptionId",
    "pdfOptionId",
    "xmlOptionId",
    "expOptionsbtnId",
    "scrDisabledTooltipId",
    "htmlOptionId_2",
    "pdfOptionId_2",
    "xmlOptionId_2",
    "expOptionsbtnEllipsisId"
  ];

  componentDidMount() {
    document.addEventListener("keydown", this.escFunction, false);
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.escFunction, false);
  }

  // To keep track of ESC key press; so exp option popover can be hidden
  escFunction(event) {
    if (event.keyCode === 27) {
      if (this.state.expOptionPopoverVisible) {
        this.setState({ expOptionPopoverVisible: false });
      }
    }
  }

  // This is called when the change to export option dropdown happens
  onExportFormatDropDownChange = value => {
    this.setState({ selectedExportOptionState: value });
    if (value === _xmlExportType) {
      // For XML, uncheck all preferences checkboxes
      this.setState({ expFilterIncStepsState: false });
      this.setState({ expFilterIncScreenshotsState: false });
      // For XML, disable all preferences checkboxes
      this.setState({ expFilterIncStepsDisabledState: true });
      this.setState({ expFilterIncScreenshotsDisabledState: true });
    } else {
      // otherwise, dont checkboxes disable based on file format
      // selection, it will happen based on some other conditions
      this.setState({ expFilterIncStepsDisabledState: false });
      this.setState({ expFilterIncScreenshotsDisabledState: false });
    }
  };

  // This is called when the Preferences/Filter option for export is changed
  onExportFilterPrefChange = e => {
    if (e.target.name === "includeSteps") {
      this.setState({ expFilterIncStepsState: e.target.checked });

      if (e.target.checked === false)
        this.setState({ expFilterIncScreenshotsState: e.target.checked });
      this.setState({
        expFilterIncScreenshotsDisabledState: !e.target.checked
      });
    }

    if (e.target.name === "includeScreenshots") {
      this.setState({ expFilterIncScreenshotsState: e.target.checked });
    }
  };

  // This is called when the default "Export" button or the "Export"
  // button inside the export options popover is clicked
  handleExportBtnClick = e => {
    let expOption = _htmlExportType;
    let filterOption = _filterNone;
    // Apply all the option for export only when "expOptionPopoverVisible"
    // is true i.e. the export option popover is visible, otherwise its a
    // default export option click whcih means exportTo=HTML, filter=none
    if (this.state.expOptionPopoverVisible) {
      if (
        !this.state.expFilterIncScreenshotsState &&
        !this.state.expFilterIncStepsState
      ) {
        filterOption = _filterNoSteps;
      } else if (!this.state.expFilterIncScreenshotsState) {
        filterOption = _filterNoImg;
      }

      if (this.state.selectedExportOptionState === _pdfExportType) {
        expOption = _pdfExportType;
      } else if (this.state.selectedExportOptionState === _xmlExportType) {
        expOption = _xmlExportType;
        // make filter option as 'none' for xml file format
        filterOption = _filterNone;
      }

      // hide popover when the exp btn is clicked.
      this.setState({ expOptionPopoverVisible: false });
    }
    this.props.exportToFormatFuncProp(expOption, filterOption);
  };

  // This is to detect a click outside the export options popover
  onClickOutsidePopover = e => {
    // Edited the condtion to use parentNode also with the update
    // of Antd to 4.x; Since in Export Ellipsis button, the click
    // on the Icon and button(small gap between) is treated diff
    if (
      Navbar.compIdsToStopPropogtationFor.includes(e.target.id) ||
      Navbar.compIdsToStopPropogtationFor.includes(
        e.target.parentNode && e.target.parentNode.id
      )
    ) {
      e.stopPropagation();
    } else {
      this.setState({
        expOptionPopoverVisible: false
      });
    }
  };

  // This is responsible to render all the contents of the export option popover
  getPopoverContent = () => {
    
    return (
      <OutsideClickHandler
        onOutsideClick={id => this.onClickOutsidePopover(id)}
      >
        <div className="popover-content">
          <div>
            <p className="choose-file-msg">
              {i18n.t("_chooseFileFormatDispStr")}
            </p>
            <Select
              aria-expanded={false}
              aria-labelledby="CORRECT IT"
              aria-label="ExportFileFormat"
              className="exp-options-sel-dropdown"
              // suffixIcon={<CaretDownOutlined />}
              defaultValue={_htmlExportType}
              onChange={this.onExportFormatDropDownChange}
            >
              {/* Ensure to add the id of any new option added to this drop 
              down inside the array "compIdsToStopPropogtationFor"*/}
              <Option
                aria-label="HtmlExportOption"
                value={_htmlExportType}
                id={Navbar.compIdsToStopPropogtationFor[0]}
              >
                {/* This option was added inside span to fix accessibility violations. 
                So also adding the same id to this span as the id of the OPtion itself */}
                <span
                  id={Navbar.compIdsToStopPropogtationFor[5]}
                  aria-label="HtmlExportOption"
                  role="textbox"
                >
                  {_htmlExportTypeDisp}
                </span>
              </Option>
              <Option
                aria-label="PdfExportOption"
                value={_pdfExportType}
                id={Navbar.compIdsToStopPropogtationFor[1]}
              >
                {/* This option was added inside span to fix accessibility violations. 
                So also adding the same id to this span as the id of the OPtion itself */}
                <span
                  id={Navbar.compIdsToStopPropogtationFor[6]}
                  aria-label="PdfExportOption"
                  role="textbox"
                >
                  {_pdfExportTypeDisp}
                </span>
              </Option>
              <Option
                aria-label="XmlExportOption"
                value={_xmlExportType}
                id={Navbar.compIdsToStopPropogtationFor[2]}
              >
                {/* This option was added inside span to fix accessibility violations. 
                So also adding the same id to this span as the id of the Option itself */}
                <span
                  id={Navbar.compIdsToStopPropogtationFor[7]}
                  aria-label="XmlExportOption"
                  role="textbox"
                >
                  {_xmlExportTypeDisp}
                </span>
              </Option>
            </Select>
          </div>
          <React.Fragment>
            <div>
              <p className="exp-set-pref-lbl">
                {i18n.t("_expFormatPrefDispStr")}
                {/* Add a info button for xml with message on why pref are disabled*/}
                <span
                // NOT addded in SCSS
                // style={{ margin: "0 0 0 5px" }}
                >
                  {this.state.selectedExportOptionState === _xmlExportType &&
                    this.getInfoSpanWithTooltip(
                      i18n.t("_msgToDisableAllExpPrefs")
                    )}
                </span>
              </p>
              <Checkbox
                name="includeSteps"
                onChange={this.onExportFilterPrefChange}
                checked={this.state.expFilterIncStepsState}
                disabled={this.state.expFilterIncStepsDisabledState}
              >
                {i18n.t("_includeStepsChkDispStr")}
              </Checkbox>
            </div>
            <div>
              <Checkbox
                name="includeScreenshots"
                onChange={this.onExportFilterPrefChange}
                checked={this.state.expFilterIncScreenshotsState}
                disabled={this.state.expFilterIncScreenshotsDisabledState}
              >
                {i18n.t("_includeScreenshotsChkDispStr")}
              </Checkbox>

              {this.state.expFilterIncScreenshotsDisabledState &&
                this.state.selectedExportOptionState !== _xmlExportType &&
                this.getInfoSpanWithTooltip(
                  i18n.t("_msgYIncScreenshotsDisabledTooltip")
                )}
            </div>
          </React.Fragment>
          <Button
            type="primary"
            className="exp-btn-in-popover"
            onClick={e => this.handleExportBtnClick(e)}
          >
            {i18n.t("_exportDispStr")}
          </Button>
        </div>
      </OutsideClickHandler>
    );
  };

  getInfoSpanWithTooltip = message => {
    return (
      <Tooltip
        id="scrDisabledTooltipId"
        arrowPointAtCenter
        placement="topRight"
        trigger={["focus", "hover"]}
        title={message}
      >
        <Button
          className="info-icon-btn"
          size="small"
          value="spanWithInfoIcon"
          aria-label="spanWithInfoIcon"
          role="button"
        >
          <InfoCircleOutlined className="ico-style" />
        </Button>
      </Tooltip>
    );
  };

  shouldDisabledExpButtons = () => {
    return this.props.expReportProp === true ||
      this.props.summaryData.executionFinished === false
      ? true
      : false;
  };

  getExpButtonsStyles() {
    return {
      verticalAlign: "top",
      borderRadius: 0,
      border: this.shouldDisabledExpButtons()
        ? "1px solid none"
        : "1px solid #0578c3"
    };
  }

  render() {
    

    let imgName = "RFT_32.png";
    let logo = null;
    try {
      logo = require("../resources/images/" + imgName);
    } catch (err) {}
    return (
      <div className="navbar">
        <span className="nav-left-logo">
          <img src={logo} alt="logo"></img>
          {/* during development, we show the hardcoded string */}
          <h1 className="nav-left-product-name">
            {process.env.NODE_ENV === "development"
              ? "HCL OneTest UI"
              : productName}
          </h1>
        </span>
        {/* the button is shown only when server is available; 
         NOT in exported report */}
        {process.env.REACT_APP_EXPORT_MODE !== _exportModeYes && (
          <ButtonGroup className="nav-right-name">
            <Tooltip
              placement="left"
              // Show tooltip only when the Export is NOT in progress,
              // as the same indicator is valid for any other type of
              // export/filter combination, so showing this generic
              // tooltip is not right when the export is in progress
              title={
                this.props.expReportProp === false
                  ? i18n.t("_defaultExpBtnTooltip")
                  : ""
              }
            >
              <Button
                // managing the color of btn inline; makes the hover/focus color go bad
                // when worked with keybaorf tab key, because the default behaviour gets
                // overridden, need to find some proper solution, hence the color is
                // managed in css class but somehtings are managed inline
                style={this.getExpButtonsStyles()}
                className="exp-btn"
                icon={
                  this.props.expReportProp === true ? (
                    <LoadingOutlined />
                  ) : (
                    <DownloadOutlined />
                  )
                }
                onClick={e => this.handleExportBtnClick(e)}
                disabled={this.shouldDisabledExpButtons()}
              >
                {this.props.expReportProp === true
                  ? i18n.t("_exportInProcess")
                  : i18n.t("_exportDispStr")}
              </Button>
            </Tooltip>
            <Popover
              placement="bottomRight"
              content={this.getPopoverContent()}
              trigger="click"
              getPopupContainer={trigger => trigger.parentNode}
              visible={this.state.expOptionPopoverVisible}
            >
              <Button
                id={Navbar.compIdsToStopPropogtationFor[3]}
                className="exp-btn"
                // managing the color of btn inline; makes the hover/focus color go bad
                // when worked with keybaorf tab key, because the default behaviour gets
                // overridden, need to find some proper solution, hence the color is
                // managed in css class but somehtings are managed inline
                style={this.getExpButtonsStyles()}
                icon={
                  <EllipsisOutlined
                    id={Navbar.compIdsToStopPropogtationFor[8]}
                  />
                }
                disabled={this.shouldDisabledExpButtons()}
                onClick={e =>
                  this.setState({
                    expOptionPopoverVisible: !this.state.expOptionPopoverVisible
                  })
                }
              ></Button>
            </Popover>
          </ButtonGroup>
        )}
      </div>
    );
  }
}

export default Navbar;
