import React, { Component } from "react";
import {
  // _passDispStr,
  // _failDispStr,
  // _testReportDispStr,
  _space
  // _testsDispStr,
  // _executedOutOfDispStr
} from "./Constants";
import { Chart } from "primereact/chart";
import i18n from "../i18n";

class SummaryChartSection extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    // Sample data for nested doughnut
    // let dataForNestedDoughnut = {
    //   labels: [
    //     "Pass",
    //     "Fail",
    //     "ChromePass",
    //     "FirefoxPass",
    //     "ChromeFail",
    //     "FirefoxFail"
    //   ],
    //   datasets: [
    //     {
    //       data: [100, 50, 0, 0, 0, 0],
    //       backgroundColor: ["green", "red", "gray", "yellow", "orange", "pink"],
    //       hoverBackgroundColor: [
    //         "green",
    //         "red",
    //         "gray",
    //         "yellow",
    //         "orange",
    //         "pink"
    //       ]
    //     },
    //     {
    //       data: [0, 0, 50, 50, 25, 25],
    //       backgroundColor: ["green", "red", "gray", "yellow", "orange", "pink"],
    //       hoverBackgroundColor: [
    //         "green",
    //         "red",
    //         "gray",
    //         "yellow",
    //         "orange",
    //         "pink"
    //       ]
    //     }
    //   ]
    // };

    const option = {
      cutoutPercentage: 65,
      animation: { animateRotate: false },
      maintainAspectRatio: true,
      responsive: true,
      tooltips: {
        // enabled: false,
        // The chart js automatically creates the tooltip from the data[] and labels[] in
        // doughnut chart. And because we have modified the labels[] by combining
        // labels[] and data[] so that the legends have number along with string
        // example : 17 Passed. So it automatically makes the tooltip like "17 Passed: 17".
        // So now using the custom method callack to edit tooltip on hover when this is called
        custom: function(tooltipModel) {
          if (tooltipModel.body !== undefined) {
            let lbl = tooltipModel.body[0].lines[0];
            tooltipModel.body[0].lines[0] = lbl.substr(
              lbl.indexOf(_space),
              lbl.length
            );
          }
        }
      },
      legend: {
        position: "right",
        labels: {
          boxWidth: 30,
          fontSize: 14,
          padding: 15
          // usePointStyle: true
        }
      }
    };
    return (
      <div className="summary-chart-section">
        <div className="summary-chart-sec-details-div">
          {i18n.t("_testReportDispStr")}
          {this.props.summaryData.executionFinished && (
            <span className="summary-chart-sec-detail-right-div">
              {/* Currently showing  "executedTests" Test at the top because since we 
            dont get to know actual executed tests out of total tests, the executed 
            will be same as Total but since the UI was written to deal with notRun, 
            inconclusive etc status, we are showing executed tests here(which will be 
            same as Total for now) */}
              {this.props.executedTests + _space}
              {/* condition to display singular or plural str */}
              {this.props.executedTests > 1
                ? i18n.t("_testsDispStr")
                : i18n.t("_testDispStr")}
            </span>
          )}
        </div>
        <Chart
          className="summary-chart-sec-doughnut-div"
          type="doughnut"
          data={this.props.doughnutDataStatus}
          // responsive="true"
          // maintainAspectRatio="true"
          options={option}
        />
      </div>
    );
  }
}

export default SummaryChartSection;
