import React, { Component } from "react";
import { Table, Checkbox } from "antd";
import {
  _failColorRed,
  _space,
  _colon,
  _vpFullDispStr,
  _listDispStr,
  isNotUndefinedAndNull,
  getDashForNullOrSame,
  getVpTableScrollY
} from "./Constants";
import i18n from "../i18n";

const { Column } = Table;

class ListVp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showListVpDiffOnly: false
    };
  }
  render() {
    return this.getListVpTable(this.props.clickedStepVpContent);
  }

  // Gets List Vp table
  getListVpTable = vpFileContent => {
    let isVpPass = true;
    let temp = vpFileContent;
    let data = [];
    temp.diff.length > 0 &&
      temp.diff.forEach(dif => {
        let item = {
          key: undefined,
          expected: undefined,
          actual: undefined,
          children: undefined
        };
        if (this.state.showListVpDiffOnly === false) {
          if (dif.match === true) {
            item.sno = dif.name;
            item.expected = getDashForNullOrSame(dif.expected);
            item.actual = getDashForNullOrSame(dif.actual);
            if (isNotUndefinedAndNull(dif.children)) {
              item.children = [];
              dif.children.forEach(child => {
                let itemChild = {
                  expected: undefined,
                  actual: undefined,
                  children: undefined,
                  key: undefined
                };
                itemChild.sno = child.name;
                itemChild.expected = getDashForNullOrSame(child.expected);
                itemChild.actual = getDashForNullOrSame(child.actual);

                itemChild.key =
                  itemChild.actual + itemChild.expected + itemChild.sno;
                item.children.push(itemChild);
              });
            }
          }
        }
        if (dif.match === false) {
          isVpPass = false;
          item.sno = dif.name;
          item.expected = getDashForNullOrSame(dif.expected);
          item.actual = (
            <span>
              <span style={{ color: _failColorRed }}>
                {getDashForNullOrSame(dif.actual)}
              </span>
            </span>
          );
          if (dif.children !== undefined) {
            item.children = [];
            dif.children.forEach(child => {
              let itemChild = {
                expected: undefined,
                actual: undefined,
                children: undefined,
                key: undefined
              };
              itemChild.sno = child.name;
              itemChild.expected = getDashForNullOrSame(child.expected);
              itemChild.actual = (
                <span>
                  <span
                    style={{ color: child.match !== true && _failColorRed }}
                  >
                    {getDashForNullOrSame(child.actual)}
                  </span>
                </span>
              );
              itemChild.key =
                itemChild.actual + itemChild.expected + itemChild.sno;
              item.children.push(itemChild);
            });
          }
        }
        // adding to data array under this condition to not have empty row when "show diff" filter is ON
        if (isNotUndefinedAndNull(item.expected)) {
          item.key = item.actual + item.expected + item.sno;
          data.push(item);
        }
      });

    let vptypeDispStr =
      _listDispStr + _space + _vpFullDispStr + _colon + _space;
    let vpResultDispStr = isVpPass
      ? i18n.t("_vpDataAsExpDispStr")
      : i18n.t("_vpDataNotAsExpDispStr");

    return (
      <React.Fragment>
        <div style={{ marginLeft: "15px", marginBottom: "5px" }}>
          <span style={{ fontWeight: "bold" }}>{vptypeDispStr}</span>
          <span>{vpResultDispStr}</span>
          <span style={{ float: "right" }}>
            <Checkbox
              onChange={this.onChangeListVpShowDiff}
              checked={this.state.showListVpDiffOnly}
              disabled={isVpPass}
            >
              {i18n.t("_showOnlyDiffDispStr")}
            </Checkbox>
          </span>
        </div>
        <Table
          style={{
            marginLeft: "15px"
          }}
          dataSource={data}
          size="small"
          bordered
          scroll={{ y: getVpTableScrollY() }}
          pagination={false}
          className="ant-table-thead antd-table-row-hover-unset"
        >
          {
            <Column
              title={i18n.t("_snoDispStr")}
              dataIndex="sno"
              key="snoListVP"
              // width={210}
            />
          }
          <Column
            className="details-table-row"
            title={
              isVpPass
                ? i18n.t("_expectedDispStr") +
                  _space +
                  i18n.t("_andDispStr") +
                  _space +
                  i18n.t("_actualDispStr")
                : i18n.t("_expectedDispStr")
            }
            dataIndex="expected"
            key="expectedListVP"
          />
          {isVpPass === false && (
            <Column
              title={i18n.t("_actualDispStr")}
              dataIndex="actual"
              key="actualListVP"
            />
          )}
        </Table>
      </React.Fragment>
    );
  };

  // This is handler for list vp checkbox tp show diff
  onChangeListVpShowDiff = e => {
    this.setState({ showListVpDiffOnly: e.target.checked });
  };
}

export default ListVp;
