// --------------Functions START from here----------
function convertTimeStampToDateStr(timestampStr) {
  var time = new Date(parseInt(timestampStr, 10));
  // return time.toLocaleString();
  return time.toDateString() + ", " + time.toLocaleTimeString();
}

// function diff(diff) {
//   // var sec = diff / 1000;
//   // var min = diff / 1000 / 60;
//   var sec = Math.round(parseInt(diff, 10) / 1000).toFixed(0);
//   var min = Math.trunc(parseInt(diff, 10) / 1000 / 60).toFixed(0);
//   var retStr = "";
//   if (min !== "0") retStr = retStr + min + " minutes";
//   else retStr = retStr + sec + " seconds";
//   return retStr;
// }

function getDuration(time) {
  time = time / 1000;
  // Hours, minutes and seconds
  var hrs = ~~(time / 3600);
  var mins = ~~((time % 3600) / 60);
  var secs = ~~time % 60;
  var ret = "";
  if (hrs > 0) {
    ret += "" + hrs + "hour " + (mins < 10 ? "0" : "");
  }
  if (mins > 0) {
    ret += "" + mins + " min " + (secs < 10 ? "0" : "");
  }
  ret += "" + secs + " sec";
  return ret;
}

function isNotUndefinedAndNull(obj) {
  if (obj !== undefined && obj !== null) return true;
}

function getVpTableScrollY() {
  return "26vh";
}

function getDashForNullOrSame(data) {
  return data === null ? _dash : data;
}

function getNoDataForNullOrSame(data) {
  return data === null ? _noData : data;
}

function setUnifiedReportVerInJson(version) {
  _unifiedRepVer = version;
}

// This method decides to show the navigate option
function shouldRenderNavigateToTestOption(ev, test) {
  // In 10.1, version field was set to "10.1" for webUI and "null" for TraditionalFT
  // In 10.1.1, this field was formalized, and was started with value 1
  // In 10.1.2, it is set to 2, to deal with show/hide Navigation icon for reports generated with older version but opened in 10.1.2
  // We need to enable the navigation icon only for reports generated on or after 10.1.2
  if (
    _unifiedRepVer === undefined ||
    _unifiedRepVer === null ||
    _unifiedRepVer === _version_10_1 ||
    parseInt(_unifiedRepVer) <= 1
  ) {
    return false;
  }

  // For the exported html report; navigation should not be present
  if (process.env.REACT_APP_EXPORT_MODE === _exportModeYes) {
    return false;
  }

  // Render only in desktop mode
  if (isServerMode()) return false;

  let eventHasLineNumber = false;
  let lineNumPropObj =
    ev !== undefined &&
    ev.additionalDetails !== undefined &&
    ev.additionalDetails.properties !== undefined &&
    // read Project Name; using filter()[0], instead of find() beacuse its not supported by IE
    ev.additionalDetails.properties.filter(prop => prop.key === _lineNumKey);
  if (
    lineNumPropObj !== undefined &&
    lineNumPropObj.length >= 1 &&
    lineNumPropObj[0].value
  ) {
    eventHasLineNumber = true;
  }

  // we should render navigateToTest link for any test whose
  //testLocation ends with ".testsuite" or ".java" or ".rftss" specially when the event
  if (
    // Todo: Need to use endsWith() instead of includes(); Also need
    // to see fix for IE as endsWith is not supported by IE using polyfill.
    (ev === undefined &&
      test !== undefined &&
      test.testLocation !== undefined &&
      test.testLocation.includes(_dotJavaExt)) ||
    (test !== undefined && test.testLocation.includes(_dotRftssExt)) ||
    (test !== undefined && test.testLocation.includes(_dotTestsuiteExt))
  ) {
    return true;
  }

  // For the FT tests, we also have some diff types of events, for which we should not
  // have the navigate option i.e. if the event (ev) has "Line Number" present as pr0perty
  return eventHasLineNumber;
}

// get the query param for the mode=desktop/container;
function isServerMode() {
  const params = new URLSearchParams(window.location.search);
  const mode = params.get(_modeDesktopOrServerKey);
  if (mode === undefined || mode !== _desktopModeVal) {
    return true;
  }
}

