import React from "react";
import {
  convertTimeStampToDateStr,
  _passStrInJson,
  _passColorGreen,
  _failColorRed,
  _aftType,
  _aftTypeDispStr,
  _testTypeCompoundDispStr,
  _testTypeSingleDispStr,
  _testTypeCompound
} from "./Constants";
import { compTestIconSvg, singleWebUISvg, aftSuiteSvg } from "./AllSvg";
import { Tooltip, Button } from "antd";
import Icon from "@ant-design/icons";
import { WarningOutlined, LoadingOutlined } from "@ant-design/icons";
import i18n from "../i18n";

const TopStatusFragment = props => {
  return (
    <div className="top-name-status-section">
      <div>
        <span className="top-name-status-section-icon">
          {getIconSvg(props)}
        </span>

        <span className="top-name-status-section-testname">
          {props.summaryData.name}
        </span>
        {props.summaryData.autoHealed && (
          <span className="top-auto-heal">{i18n.t("_autoHealApplied")}</span>
        )}
        <span
          className="top-status-flag"
          style={{
            backgroundColor:
              props.summaryData.rollUpVerdict === _passStrInJson
                ? _passColorGreen
                : _failColorRed
          }}
        >
          {/* // For pass case, we need to check for aborted case and show Pass string accordingly */}
          {props.summaryData.rollUpVerdict === _passStrInJson
            ? i18n.t("_passedDispStr")
            : i18n.t("_failedDispStr")}
          {props.summaryData.executionAborted === true && (
            <Tooltip
              title={i18n.t("_abortedTooltipDispStr")}
              arrowPointAtCenter
              placement="topLeft"
              trigger={["focus", "hover"]}
            >
              <WarningOutlined className="top-status-flag-abort-icon" />
            </Tooltip>
          )}
        </span>
        <span>
          {props.summaryData.executionFinished === false && (
            <LoadingOutlined
              className="top-status-loadiing-icon"
              style={{
                color:
                  props.summaryData.rollUpVerdict === _passStrInJson
                    ? _passColorGreen
                    : _failColorRed
              }}
            />
          )}
        </span>
        <span style={{ float: "right" }}>
          <span className="top-status-time">
            {convertTimeStampToDateStr(props.summaryData.reportGeneratedTime)}
          </span>
        </span>
      </div>
      <div className="top-name-status-line2">
        {props.summaryData.testLocation}
      </div>
    </div>
  );
};

export default TopStatusFragment;

function getIconSvg(props) {
  const type = props.summaryData.testType;
  if (type === _aftType) {
    return (
      <Tooltip
        title={_aftTypeDispStr}
        arrowPointAtCenter
        placement="topLeft"
        trigger={["focus", "hover"]}
      >
        <Button
          size="small"
          value="buttonWithStatusIcon"
          aria-label="buttonWithStatusIcon"
          role="button"
          className="ico-btn"
        >
          <Icon
            aria-label="Aft Suite Type Icon"
            alt="AftSuiteTypeIcon"
            className="ico-svg"
            component={aftSuiteSvg}
          />
        </Button>
      </Tooltip>
    );
  } else if (type === _testTypeCompound) {
    return (
      <Tooltip
        title={_testTypeCompoundDispStr}
        arrowPointAtCenter
        placement="topLeft"
        trigger={["focus", "hover"]}
      >
        <Button
          size="small"
          value="buttonWithStatusIcon"
          aria-label="buttonWithStatusIcon"
          role="presentation"
          className="ico-btn"
        >
          <Icon
            aria-label="Compound Test Type Icon"
            alt="compTestTypeIcon"
            className="ico-svg"
            component={compTestIconSvg}
          />
        </Button>
      </Tooltip>
    );
  } else {
    return (
      <Tooltip
        title={_testTypeSingleDispStr}
        arrowPointAtCenter
        placement="topLeft"
        trigger={["focus", "hover"]}
      >
        <Button
          size="small"
          value="buttonWithStatusIcon"
          aria-label="buttonWithStatusIcon"
          role="presentation"
          className="ico-btn"
        >
          <Icon
            aria-label="Single Test Type Icon"
            alt="singleTestTypeIcon"
            className="ico-svg"
            component={singleWebUISvg}
          />
        </Button>
      </Tooltip>
    );
  }
}
