import React, { Component } from "react";
import SummaryChartSection from "./SummaryChartSection";
import SummaryTabsSection from "./SummaryTabsSection";
import { Row, Col, Button } from "antd";
import chromeLogo from "../resources/images/chrome.png";
import firefoxLogo from "../resources/images/firefox.png";
import edgeLogo from "../resources/images/edge.png";
import ieLogo from "../resources/images/ie.png";
import safariLogo from "../resources/images/safari.png";
import operaLogo from "../resources/images/opera.png";
import win10Logo from "../resources/images/windows10.jpg";
import win11Logo from "../resources/images/windows11.png";
import win7Logo from "../resources/images/windows7.jpg";
import infoLogo from "../resources/images/info.png";
import macLogo from "../resources/images/mac_os_X.png";
import linuxLogo from "../resources/images/linux.jpg";
import ubuntuLogo from "../resources/images/ubuntu.png";
import redHatLogo from "../resources/images/redhat.jpg";

import { Badge, Tooltip } from "antd";
import {
  _summarySecColorsForDoughnut,
  _failStrInJson,
  _passStrInJson,
  convertTimeStampToDateStr,
  _counterTestsInJson,
  _counterTotalInJson,
  _counterBrowserWiseTestInJson,
  _browserChromeInJson,
  _browserFirefoxInJson,
  _browserEdgeInJson,
  _browserSafariInJson,
  _browserieInJson,
  _browserieInJson2,
  _colon,
  _space,
  _win10DispStr,
  _win11DispStr,
  _win7DispStr,
  _macOsXDispStr,
  getDuration,
  _inconclusiveStrInJson,
  _notrunStrInJson,
  _comma,
  _bracesOpen,
  _bracesClose,
  _browserOperaInJson,
  _bracesSqClose,
  _bracesSqOpen,
  _linuxDispStr,
  _linuxUbuntuDispStr,
  _linuxRedHatDispStr,
  _browserEdgeInJson2
  // _inconclusiveDispStr,
  // _notrunDispStr
} from "./Constants";
import {
  DesktopOutlined,
  WindowsOutlined,
  ChromeOutlined,
  ClockCircleOutlined,
  HourglassOutlined
} from "@ant-design/icons";
import i18n from "../i18n";

class SummarySection extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    let totalTests = 0;
    let executedTests = 0;
    const colorsForDoughnut = _summarySecColorsForDoughnut;

    let counters = this.props.summaryData.counters;
    let counterLabelsStatus = [];
    let counterDataStatus = [];
    let counterDataColorsStatus = [];

    let counterLabelsBrowser = [];
    let counterDataBrowser = [];

    // this loop does the count of tests and browser wise test
    counters.forEach(counter => {
      if (counter.type === _counterTestsInJson) {
        let colorNum = 0;
        counter.value.forEach(val => {
          if (val.key === _counterTotalInJson) {
            totalTests = val.value;
          }
          if (val.key !== _counterTotalInJson) {
            if (val.key === _passStrInJson) {
              counterLabelsStatus.push(
                val.value + _space + i18n.t("_passedDispStr1")
              );
              executedTests = executedTests + val.value;
            } else if (val.key === _failStrInJson) {
              counterLabelsStatus.push(
                val.value + _space + i18n.t("_failedDispStr1")
              );
              executedTests = executedTests + val.value;
            } else if (val.key === _inconclusiveStrInJson) {
              counterLabelsStatus.push(
                val.value + _space + i18n.t("_inconclusiveDispStr")
              );
              executedTests = executedTests + val.value;
            } else if (val.key === _notrunStrInJson) {
              counterLabelsStatus.push(
                val.value + _space + i18n.t("_notrunDispStr")
              );
            }
            counterDataStatus.push(val.value);
            counterDataColorsStatus[colorNum] = colorsForDoughnut[colorNum];
            colorNum++;
          }
        });
      } else if (counter.type === _counterBrowserWiseTestInJson) {
        counter.value.forEach(val => {
          counterLabelsBrowser.push(val.key);
          counterDataBrowser.push(val.value);
        });
      }
    });

    // Setting Status Doughnut details
    let doughnutDataStatus = {};
    let doughnutDatasetStatus = [];
    let doughnutDatasetStatusOthElement = {};
    doughnutDatasetStatusOthElement.data = counterDataStatus;
    doughnutDatasetStatusOthElement.backgroundColor = counterDataColorsStatus;
    doughnutDatasetStatusOthElement.borderWidth = 1; // manage gap between doughnut arc
    doughnutDatasetStatusOthElement.hoverBackgroundColor = counterDataColorsStatus;
    doughnutDataStatus.labels = counterLabelsStatus;
    doughnutDatasetStatus[0] = doughnutDatasetStatusOthElement;
    doughnutDataStatus.datasets = doughnutDatasetStatus;
    //-----

    //Setting things to show in Tabs
    let tab1SummaryListItem = [];
    let tab2ResourcesListItem = [];

    // For Environment
    const env = this.props.summaryData.environment;
    let tab1AUTObj = {
      key: "",
      value: "",
      valuesTypeArr: [],
      valuesArr: [],
      valuesIconArr: []
    };
    // condition for the key to have a singular or plural str
    tab1AUTObj.key =
      env.aut.length > 1 ? i18n.t("_envAutsDispStr") : i18n.t("_envAutDispStr");
    for (var i = 0; i < env.aut.length; i++) {
      // putting extra details for AUTs; to be able to show
      // icon based on type of AUT and not creating the string here
      tab1AUTObj.valuesArr.push(env.aut[i].name);
      tab1AUTObj.valuesTypeArr.push(env.aut[i].type);
      tab1AUTObj.valuesIconArr.push(env.aut[i].icon);
    }
    tab1AUTObj.icon = <DesktopOutlined className="summary-tabs-ul-li-icon" />;
    tab1SummaryListItem.push(tab1AUTObj);

    // parse platform info
    let tab2PlatformObj = { key: "", value: "" };
    tab2PlatformObj.key = i18n.t("_envPlatformDispStr");
    for (i = 0; i < env.platform.length; i++) {
      tab2PlatformObj.value =
        tab2PlatformObj.value +
        env.platform[i].name +
        _space +
        _bracesOpen +
        env.platform[i].architecture +
        _bracesClose;
      if (i < env.platform.length - 1)
        tab2PlatformObj.value = tab2PlatformObj.value + _comma + _space;
    }

    let osListed = [];
    // returning null because with use of map() its expected to return a obj,
    // or else gives a warning
    tab2PlatformObj.value = env.platform.map(platform => {
      let tooltipArr = [platform.name];
      if (platform.name.includes(_win11DispStr)) {
        if (osListed.includes(_win11DispStr) === true) return null;
        osListed.push(_win11DispStr);
        return this.getBrowserAndBadge(win11Logo, "", tooltipArr);
      } else if (platform.name.includes(_win10DispStr)) {
        if (osListed.includes(_win10DispStr) === true) return null;
        osListed.push(_win10DispStr);
        return this.getBrowserAndBadge(win10Logo, "", tooltipArr);
      } else if (platform.name.includes(_win7DispStr)) {
        if (osListed.includes(_win7DispStr) === true) return null;
        osListed.push(_win7DispStr);
        return this.getBrowserAndBadge(win7Logo, "", tooltipArr);
      } else if (platform.name.includes(_macOsXDispStr)) {
        if (osListed.includes(_macOsXDispStr) === true) return null;
        osListed.push(_macOsXDispStr);
        return this.getBrowserAndBadge(macLogo, "", tooltipArr);
      } else if (platform.name.includes(_linuxDispStr)) {
        if (platform.name.includes(_linuxUbuntuDispStr)) {
          if (osListed.includes(_linuxUbuntuDispStr) === true) return null;
          osListed.push(_linuxUbuntuDispStr);
          return this.getBrowserAndBadge(ubuntuLogo, "", tooltipArr);
        } else if (platform.name.includes(_linuxRedHatDispStr)) {
          if (osListed.includes(_linuxRedHatDispStr) === true) return null;
          osListed.push(_linuxRedHatDispStr);
          return this.getBrowserAndBadge(redHatLogo, "", tooltipArr);
        } else {
          if (osListed.includes(platform.name) === true) return null;
          osListed.push(platform.name);
          return this.getBrowserAndBadge(linuxLogo, "", tooltipArr);
        }
      } else return this.getBrowserAndBadge(infoLogo, "", tooltipArr);
    });

    tab2PlatformObj.icon = (
      <WindowsOutlined className="summary-tabs-ul-li-icon" />
    );

    tab2ResourcesListItem.push(tab2PlatformObj);

    // Application Runtime item in Resources tab
    let tab2AppRuntimeObj = { key: "", value: "" };
    tab2AppRuntimeObj.key = i18n.t("_applicationRuntimeDispStr");
    // loop through counterLabelsBrowser and create icon And badge for each item,
    // for tooltip, look for each item's name in "env.configuration",
    // i.e. basically get all items with same name and create tooltip array of them
    tab2AppRuntimeObj.value = counterLabelsBrowser.map((configName, brIndx) => {
      let badge = counterDataBrowser[brIndx];

      // filter to get array of config  with name
      let allConfEntriesWithName = env.configuration.filter(
        conf => conf.name === configName
      );
      let confTooltipArr = [];
      // loop through to get tooltip array
      allConfEntriesWithName.forEach(conf => {
        let mode = "";
        if (conf.mode !== undefined && conf.mode !== "")
          mode = _bracesSqOpen + conf.mode + _bracesSqClose + _space;

        let confVerAndArch = "";
        let confVer = "";
        if (conf.version !== undefined && conf.version !== "") {
          confVer = conf.version;
          confVerAndArch = confVerAndArch + confVer;
        }

        let confArch = "";
        if (conf.architecture !== undefined && conf.architecture !== "") {
          confArch = conf.architecture;
          if (confVerAndArch !== "") {
            // if the version is also part of "confVerAndArch", only then appane a comma before "arch"
            confVerAndArch = confVerAndArch + _comma + _space + confArch;
          } else {
            confVerAndArch = confVerAndArch + confArch;
          }
        }

        // if there is no arch and version, then to avoid black braces
        if (confVerAndArch !== "") {
          confVerAndArch = _bracesOpen + confVerAndArch + _bracesClose;
        }
        let tooltip = conf.name + _space + mode + _space + confVerAndArch;
        confTooltipArr.push(tooltip);
      });

      // create tooltip for total Tests and add to array
      let totalTestsTootltip =
        badge > 1 ? i18n.t("_totalTestsDispStr") : i18n.t("_totalTestDispStr");
      totalTestsTootltip = totalTestsTootltip + _colon + _space + badge;
      confTooltipArr.push(totalTestsTootltip);

      if (configName.startsWith(_browserChromeInJson))
        return this.getBrowserAndBadge(chromeLogo, badge, confTooltipArr);
      else if (configName.startsWith(_browserFirefoxInJson))
        return this.getBrowserAndBadge(firefoxLogo, badge, confTooltipArr);
      else if (
        configName.startsWith(_browserEdgeInJson) ||
        configName.startsWith(_browserEdgeInJson2)
      )
        return this.getBrowserAndBadge(edgeLogo, badge, confTooltipArr);
      else if (configName.startsWith(_browserSafariInJson))
        return this.getBrowserAndBadge(safariLogo, badge, confTooltipArr);
      else if (
        configName.startsWith(_browserieInJson) ||
        configName.startsWith(_browserieInJson2)
      )
        return this.getBrowserAndBadge(ieLogo, badge, confTooltipArr);
      else if (configName.startsWith(_browserOperaInJson))
        return this.getBrowserAndBadge(operaLogo, badge, confTooltipArr);
      else return this.getBrowserAndBadge(infoLogo, badge, confTooltipArr);
    });

    tab2AppRuntimeObj.icon = (
      <ChromeOutlined className="summary-tabs-ul-li-icon" />
    );
    tab2ResourcesListItem.push(tab2AppRuntimeObj);

    let tab1StartTimeObj = { key: "", value: "" };
    tab1StartTimeObj.key = i18n.t("_summTabStartTimeDispStr");
    tab1StartTimeObj.icon = (
      <ClockCircleOutlined className="summary-tabs-ul-li-icon" />
    );
    tab1StartTimeObj.value = convertTimeStampToDateStr(
      this.props.summaryData.startTime
    );
    tab1SummaryListItem.push(tab1StartTimeObj);

    let tab1EndTimeObj = { key: "", value: "" };
    tab1EndTimeObj.key = i18n.t("_summTabEndTimeDispStr");
    tab1EndTimeObj.icon = (
      <ClockCircleOutlined rotate={180} className="summary-tabs-ul-li-icon" />
    );
    tab1EndTimeObj.value = convertTimeStampToDateStr(
      this.props.summaryData.endTime
    );
    tab1SummaryListItem.push(tab1EndTimeObj);

    let tab1DurationObj = { key: "", value: "" };
    tab1DurationObj.key = i18n.t("_durationDispStr");
    tab1DurationObj.icon = (
      <HourglassOutlined className="summary-tabs-ul-li-icon" />
    );
    tab1DurationObj.value = getDuration(this.props.summaryData.executionTime);
    tab1SummaryListItem.push(tab1DurationObj);

    return (
      <Row>
        <Col span={9}>
          <div>
            <SummaryChartSection
              // Sending totalTest and executedTests as props to avoid looping
              // to find later, as here it already happens
              totalTests={totalTests}
              executedTests={executedTests}
              summaryData={this.props.summaryData}
              doughnutDataStatus={doughnutDataStatus}
            />
          </div>
        </Col>
        <Col span={15}>
          <div className="summary-tabs-section">
            <SummaryTabsSection
              summaryData={this.props.summaryData}
              counterLabels={counterLabelsStatus}
              counteData={counterDataStatus}
              executionTime={this.props.summaryData.executionTime}
              tab1ListData={tab1SummaryListItem}
              tab2ListData={tab2ResourcesListItem}
            />
          </div>
        </Col>
      </Row>
    );
  }

  // returns an Avatar for logo image passed with a badge specified in "badgeNumber" and a tooltip
  getBrowserAndBadge(logo, badgeNmber, tooltipArr) {
    return (
      <Tooltip
        trigger={["focus", "hover"]}
        autoAdjustOverflow={false}
        key={logo + badgeNmber + tooltipArr}
        title={
          <div>
            {tooltipArr !== undefined &&
              tooltipArr.length > 0 &&
              tooltipArr.map(tooltip => {
                return <div key={tooltip}>{tooltip}</div>;
              })}
          </div>
        }
        placement="top"
      >
        <span className="app-runtime-span">
          {/* adding space as title avoids badge hover tooltip as we have a tootltip already */}
          <Badge
            title={_space}
            count={badgeNmber}
            className="app-runtime-span-badge"
          >
            {/* <Avatar size={25} src={logo} alt="logo" /> */}
            {/* have put the img inside button for accessibility */}
            <Button
              size="small"
              value="buttonWithLogoImg"
              aria-label="buttonWithLogoImg"
              role="button"
              className="app-runtime-span-btnimg"
            >
              <img
                width={22}
                src={logo}
                alt="logo"
                className="app-runtime-span-img"
              ></img>
            </Button>
          </Badge>
        </span>
      </Tooltip>
    );
  }
}

export default SummarySection;
