import React, { Component } from "react";
import { Tree, Table, Checkbox, Tooltip, Button, Modal, Divider } from "antd";
import {
  _passStrInJson,
  _failColorRed,
  _space,
  _colon,
  _vpFullDispStr,
  isNotUndefinedAndNull,
  _propertiesNode,
  _failStrInJson,
  getVpTableScrollY,
  getNoDataForNullOrSame,
  _vpTypeState
} from "./Constants";
import ReactHtmlParser from "react-html-parser";
import { ZoomInOutlined } from "@ant-design/icons";
import i18n from "../i18n";

const { TreeNode } = Tree;
const { Column } = Table;

const CONST = {
  ACTUAL: "actual",
  EXPECTED: "expected",
  COLOR_BLACK: "black",
  COLOR_GRAY: "gray"
};

class GenericVpWithTreeNodeAndProperties extends Component {
  constructor(props) {
    super(props);
    // Global variables for keeping track of default keys for both actual and expected,
    // that show expanded by default i.e. all keys except properties(and its children) nodes.
    // And also keeping track of the Properties and its chilresn as part of single loop but
    // store in diff variable. And then combine both these array nodes when Full Expand button
    // is selected or remove all when collapse all is selected
    this.nodesActGlobal = {
      defaultkeysToExpand: [],
      keysForPropNodes: []
    };
    this.nodesExpGlobal = {
      defaultKeysToExpand: [],
      keysForPropNodes: []
    };
    this.state = {
      // managing the sync between the two trees using these state variables
      keysToExpandActual: undefined,
      keysToExpandExpected: undefined,
      showDiffOnly: false,
      showVpFull: false,
      expandTree: true
    };
  }

  componentWillReceiveProps() {
    this.setState({ keysToExpandActual: undefined });
    this.setState({ keysToExpandExpected: undefined });
    // current behaviour is to clear the checkbox when switched to other step
    this.setState({ showDiffOnly: false });
    // to control the behaviour of exapnd/collpase button when multiple vps are present
    this.setState({ expandTree: true });
  }

  render() {
    // ensuring to clear the array before render
    this.nodesActGlobal.defaultkeysToExpand = [];
    this.nodesActGlobal.keysForPropNodes = [];
    this.nodesExpGlobal.defaultKeysToExpand = [];
    this.nodesExpGlobal.keysForPropNodes = [];

    // rendered filtered nodes if "show diff is checked"
    if (this.state.showDiffOnly === true) {
      return this.getVpTable({
        diff: filterWithRollUp(this.props.clickedStepVpContent.diff)
      });
    }
    // other wise show all nodes in tree
    return this.getVpTable(this.props.clickedStepVpContent);
  }

  // Gets Vp table with actual and expected tree
  getVpTable = vpFileContent => {
    // assuming if the step is failed then the vp itself has failed
    let isVpPass =
      this.props.clickedStepDetails.status === _passStrInJson ? true : false;
    let temp = vpFileContent;
    let data = [];
    let item = {
      expected: undefined,
      actual: undefined,
      children: undefined
    };
    // currently used to give unique int key to each node,
    // then we use this to manage the sync between the trees
    // and also to manage expand/collapse all functionality
    let currNodeActCounter = { count: 0 };

    item.actual = (
      <Tree
        showLine={{ showLeafIcon: false }}
        defaultExpandAll
        selectable={false}
        // during first render/default render the state will be undefined
        // and we show expanded the defaultKeys(i.e. all keys except the
        // Properties node and its children). And if the user does any action like
        // expand/collapse All or any specific node, we change the state variable
        // and this code here then uses the state variable to decide on expansion
        expandedKeys={
          this.state.keysToExpandActual === undefined
            ? this.nodesActGlobal.defaultkeysToExpand
            : this.state.keysToExpandActual
        }
        // expandedKeys={this.state.keysToExpandActual}
        onExpand={this.handleExpandActual}
        key={this.props.clickedStepDetails.eventUid + CONST.ACTUAL}
      >
        {temp.diff !== undefined &&
          temp.diff.length > 0 &&
          temp.diff.map(dif => {
            return isNotUndefinedAndNull(dif.children) ? (
              this.renderTreeNodes(dif, CONST.ACTUAL, currNodeActCounter)
            ) : (
              <TreeNode
                title={
                  <span style={this.getNodeColor(dif, CONST.ACTUAL)}>
                    {this.getTrucatedTitleAndTooltip(
                      getNoDataForNullOrSame(dif.actual)
                    )}
                  </span>
                }
                key={++currNodeActCounter.count}
              >
                {this.addToDefaultExpandedNodesArr(
                  CONST.ACTUAL,
                  currNodeActCounter.count + ""
                )}

                {dif.attributes !== undefined &&
                  this.getPropertiesAttribute(
                    dif,
                    CONST.ACTUAL,
                    currNodeActCounter
                  )}
              </TreeNode>
            );
          })}
      </Tree>
    );

    let currNodeExpCounter = { count: 0 };
    item.expected = (
      <Tree
        showLine={{ showLeafIcon: false }}
        defaultExpandAll
        selectable={false}
        expandedKeys={
          this.state.keysToExpandExpected === undefined
            ? this.nodesExpGlobal.defaultKeysToExpand
            : this.state.keysToExpandExpected
        }
        // expandedKeys={this.state.keysToExpandExpected}
        onExpand={this.handleExpandExpected}
        key={this.props.clickedStepDetails.eventUid + CONST.EXPECTED}
      >
        {temp.diff !== undefined &&
          temp.diff.length > 0 &&
          temp.diff.map(dif => {
            return isNotUndefinedAndNull(dif.children) ? (
              this.renderTreeNodes(dif, CONST.EXPECTED, currNodeExpCounter)
            ) : (
              <TreeNode
                title={
                  <span style={this.getNodeColor(dif, CONST.EXPECTED)}>
                    {this.getTrucatedTitleAndTooltip(
                      getNoDataForNullOrSame(dif.expected)
                    )}
                  </span>
                }
                key={++currNodeExpCounter.count}
              >
                {this.addToDefaultExpandedNodesArr(
                  CONST.EXPECTED,
                  currNodeExpCounter.count + ""
                )}

                {dif.attributes !== undefined &&
                  this.getPropertiesAttribute(
                    dif,
                    CONST.EXPECTED,
                    currNodeExpCounter
                  )}
              </TreeNode>
            );
          })}
      </Tree>
    );

    data.push(item);

    let vptypeDispStr =
      this.props.vpTypeForDisplay + _space + _vpFullDispStr + _colon + _space;
    let vpResultDispStr = isVpPass
      ? i18n.t("_vpDataAsExpDispStr")
      : i18n.t("_vpDataNotAsExpDispStr");

    // show the tree in a Modal according to condition
    if (this.state.showVpFull) {
      return (
        <Modal
          style={{ top: 20 }}
          width="85%"
          title={ReactHtmlParser(this.props.clickedStepDetails.description)}
          visible={this.state.showVpFull}
          onCancel={this.handleVpFullCancel}
          footer={null}
        >
          {this.getVpDetailsTable(
            vptypeDispStr,
            vpResultDispStr,
            isVpPass,
            data
          )}
        </Modal>
      );
    }

    // otherwise show the tree in the step details section(small area for step details)
    return this.getVpDetailsTable(
      vptypeDispStr,
      vpResultDispStr,
      isVpPass,
      data
    );
  };

  // This is utitlity method used to render menu vp table
  renderTreeNodes = (node, val, currNode) => {
    return (
      <TreeNode
        onLoad={this.handleLoadAct}
        title={
          <span style={this.getNodeColor(node, val)}>
            {this.getTrucatedTitleAndTooltip(getNoDataForNullOrSame(node[val]))}
          </span>
        }
        key={++currNode.count}
      >
        {this.addToDefaultExpandedNodesArr(val, currNode.count + "")}

        {node.attributes !== undefined &&
          this.getPropertiesAttribute(node, val, currNode)}
        {node.children.map(child => {
          return child.children === undefined ? (
            <TreeNode
              title={
                <span style={this.getNodeColor(child, val)}>
                  {this.getTrucatedTitleAndTooltip(
                    getNoDataForNullOrSame(child[val])
                  )}
                </span>
              }
              key={++currNode.count}
            >
              {this.addToDefaultExpandedNodesArr(val, currNode.count + "")}

              {child.attributes !== undefined &&
                this.getPropertiesAttribute(child, val, currNode)}
            </TreeNode>
          ) : (
            this.renderTreeNodes(child, val, currNode)
          );
        })}
      </TreeNode>
    );
  };

  // Gets a Treenode with a node title Properrties and all proerties as its children
  getPropertiesAttribute = (node, val, currNode) => {
    let attributes = node.attributes;
    if (this.state.showDiffOnly) {
      attributes = attributes.filter(attr => attr.match === false);
    }
    // to avoid showing the hardcoded Properties node if there are no properties after filter
    if (attributes !== undefined && attributes.length <= 0) return;
    return (
      <TreeNode
        style={{
          fontStyle: "italic"
        }}
        title={
          <span style={this.getPropNodeStyle(node.match, val, _propertiesNode)}>
            {_propertiesNode}
          </span>
        }
        key={++currNode.count}
      >
        {this.addToPropAndChildrenNodesArr(val, currNode.count + "")}
        {/* for state vp, make the properties and its childnodes render in the same way 
        as default nodes, so add these keys to nodesActGlobal.defaultkeysToExpand*/}
        {this.props.vpType === _vpTypeState &&
          this.addToDefaultExpandedNodesArr(val, currNode.count + "")}

        {attributes !== undefined &&
          attributes.map(attr => {
            return (
              <TreeNode
                title={
                  <span
                    style={this.getPropNodeStyle(attr.match, val, attr.name)}
                  >
                    {getNoDataForNullOrSame(attr.name)}
                  </span>
                }
                key={++currNode.count}
              >
                {this.addToPropAndChildrenNodesArr(val, currNode.count + "")}
                {/* for state vp, make the properties and its childnodes render in the same way 
                as default nodes, so add these keys to nodesActGlobal.defaultkeysToExpand*/}
                {this.props.vpType === _vpTypeState &&
                  this.addToDefaultExpandedNodesArr(val, currNode.count + "")}
                <TreeNode
                  title={
                    <span
                      style={this.getPropNodeStyle(attr.match, val, attr[val])}
                    >
                      {getNoDataForNullOrSame(attr[val]) + ""}
                    </span>
                  }
                  key={++currNode.count}
                >
                  {this.addToPropAndChildrenNodesArr(val, currNode.count + "")}
                  {/* for state vp, make the properties and its childnodes render in the same way 
                  as default nodes, so add these keys to nodesActGlobal.defaultkeysToExpand*/}
                  {this.props.vpType === _vpTypeState &&
                    this.addToDefaultExpandedNodesArr(val, currNode.count + "")}
                  {/* For nested attributes */}
                  {attr.attributes !== undefined &&
                    this.getPropertiesAttribute(attr, val, currNode)}
                </TreeNode>
              </TreeNode>
            );
          })}
      </TreeNode>
    );
  };

  // The returns the table with the vp tree(actual and expected).
  // This is reused when viewd in zoom/full screen mode
  getVpDetailsTable = (vptypeDispStr, vpResultDispStr, isVpPass, data) => {
    return (
      <React.Fragment>
        <div
          style={{
            marginLeft: "15px",
            marginBottom: "5px",
            overflow: "hidden",
            textOverflow: "ellipsis"
          }}
        >
          <span
            style={{
              fontWeight: "bold"
            }}
          >
            {vptypeDispStr}
          </span>
          <span>{vpResultDispStr}</span>
          <div style={{ float: "right" }}>
            <Button
              style={{ padding: "0 5px" }}
              // type="link"
              onClick={
                this.state.expandTree
                  ? this.onClickCollapseTree
                  : this.onClickExpandTree
              }
            >
              {this.state.expandTree
                ? i18n.t("_collapseVpTree")
                : i18n.t("_expandVpTree")}
            </Button>
            <Divider type="vertical" style={{ height: "1.5em" }} />
            <Checkbox
              onChange={this.onChangeShowDiff}
              checked={this.state.showDiffOnly}
              disabled={isVpPass}
            >
              {i18n.t("_showOnlyDiffDispStr")}
            </Checkbox>
            {/* Dont show the zoom btn in full mode */}
            {this.state.showVpFull === false && (
              <React.Fragment>
                <Divider type="vertical" style={{ height: "1.5em" }} />
                <Tooltip title={i18n.t("_showImgVpFullScDispStr")}>
                  <Button
                    size="small"
                    icon={<ZoomInOutlined />}
                    onClick={this.onClickshowVpFull}
                  >
                    {/* Show Full */}
                  </Button>
                </Tooltip>
              </React.Fragment>
            )}
          </div>
        </div>
        <Table
          rowClassName="vp-tree-types-table-row"
          style={{
            marginLeft: "15px"
          }}
          dataSource={data}
          size="small"
          bordered
          scroll={{
            y: this.state.showVpFull ? "70vh" : getVpTableScrollY()
          }}
          pagination={false}
          className="ant-table-thead antd-table-row-hover-unset"
        >
          <Column
            className="details-table-row"
            title={
              isVpPass
                ? i18n.t("_expectedDispStr") +
                  _space +
                  i18n.t("_andDispStr") +
                  _space +
                  i18n.t("_actualDispStr")
                : i18n.t("_expectedDispStr")
            }
            dataIndex="expected"
            key="expectedTreeVP"
            ellipsis={true}
          />
          {isVpPass === false && (
            <Column
              className="details-table-row"
              title={i18n.t("_actualDispStr")}
              dataIndex="actual"
              key="actualTreeVP"
              ellipsis={true}
            />
          )}
        </Table>
      </React.Fragment>
    );
  };

  // Gets the node color style based on the match property. If the match is false
  // then the node is RED, other wise BLACK. If the node's actual & expected
  // are null, then the style of such a node is diff
  getNodeColor = (node, val) => {
    // If node[val] is null and render diff style for such nodes for fail and pass case
    if (node[val] === null) {
      if (node.match === false) {
        return this.getFailNullNodeStyle(val);
      } else if (node.match === true) {
        return this.getPassNullNodeStyle();
      }
    }
    return {
      color:
        node.match === false && val === CONST.ACTUAL
          ? _failColorRed
          : CONST.COLOR_BLACK
    };
  };

  // Render style for Proerties and its children nodes. This is separate method
  // because the property node is dummy hence the val is null and also its
  // children, the name property has val/data is null but must be rendered
  // differently, so this is separate method
  getPropNodeStyle = (match, val, data) => {
    if (data === null && match === false) {
      return this.getFailNullNodeStyle(val);
    } else if (data === null) {
      return this.getPassNullNodeStyle();
    }
    return {
      color:
        match === false && val === CONST.ACTUAL
          ? _failColorRed
          : CONST.COLOR_GRAY
    };
  };

  // Style for all Passed nodes with data as null
  getPassNullNodeStyle = () => {
    return {
      fontSize: "12px",
      color: "#e0e0e0"
    };
  };

  // Style for all Failed nodes with data as null
  getFailNullNodeStyle = val => {
    return {
      fontSize: "12px",
      color: "#e0e0e0",
      backgroundColor: val === CONST.ACTUAL ? "#e4636312" : "transparent"
    };
  };

  // Adds the all the nodes to "nodesActGlobal.defaultkeysToExpand"
  // as these are the nodes that are expanded by default
  // (i.e. all excluding the node "Properties" and its children)
  addToDefaultExpandedNodesArr = (val, key) => {
    val === CONST.ACTUAL
      ? this.nodesActGlobal.defaultkeysToExpand.push(key)
      : this.nodesExpGlobal.defaultKeysToExpand.push(key);
  };

  // Adds the node "Properties and its children to
  // "nodesActGlobal.keysForPropNodes". As these are the nodes NOT
  // expanded by default but need to be expanded when user selects "Expand All"
  addToPropAndChildrenNodesArr = (val, key) => {
    val === CONST.ACTUAL
      ? this.nodesActGlobal.keysForPropNodes.push(key)
      : this.nodesExpGlobal.keysForPropNodes.push(key);
  };

  // handle expand to sync the trees
  handleExpandActual = (expandedKeys, expanded, node) => {
    this.setState({ keysToExpandActual: expandedKeys });
    this.setState({ keysToExpandExpected: expandedKeys });
  };

  // handle expand to sync the trees
  handleExpandExpected = (expandedKeys, expanded, node) => {
    this.setState({ keysToExpandActual: expandedKeys });
    this.setState({ keysToExpandExpected: expandedKeys });
  };

  // This is handler for show diff checkbox
  onChangeShowDiff = e => {
    this.setState({ showDiffOnly: e.target.checked });
  };

  // This sets the state on click of button to open full vp Mode
  onClickshowVpFull = e => {
    this.setState({ showVpFull: true });
  };

  // to handle the expand all nodes. We combine the two arrays we maintain,
  // i.e. one for all nodes that we show expanded by default except the
  //properties nodes and its children and the other array of properties and its children.
  // Basically this merging of two array of nodes means, expand all the nodes
  onClickExpandTree = e => {
    this.setState({
      keysToExpandActual: [
        ...this.nodesActGlobal.defaultkeysToExpand,
        ...this.nodesActGlobal.keysForPropNodes
      ]
    });
    this.setState({
      keysToExpandExpected: [
        ...this.nodesExpGlobal.defaultKeysToExpand,
        ...this.nodesExpGlobal.keysForPropNodes
      ]
    });
    this.setState({ expandTree: true });
  };

  // to handle collapse all, delete all nodes, meaning, exapnd none
  onClickCollapseTree = e => {
    this.setState({
      keysToExpandActual: []
    });
    this.setState({
      keysToExpandExpected: []
    });
    this.setState({ expandTree: false });
  };

  // Cancel.clicked outside the zoom window, makes to disappear
  handleVpFullCancel = e => {
    this.setState({ showVpFull: false });
  };

  // Returns a Tooltip obj after truncating the str.
  //Truncation happens only if the step has failed.
  //And tooltip is applied only if the string has been truncated
  getTrucatedTitleAndTooltip = str => {
    if (this.state.showVpFull) return str;
    return (
      <Tooltip
        mouseEnterDelay={1}
        placement="topRight"
        title={
          this.props.clickedStepDetails.status === _passStrInJson
            ? undefined
            : str
        }
      >
        {this.props.clickedStepDetails.status === _failStrInJson
          ? this.truncate(str)
          : str}
      </Tooltip>
    );
  };

  // Truncate a String
  truncate = str => {
    if (this.state.showVpFull) return str;
    return str && str.length > 40 ? str.substring(0, 40) + "..." : str;
  };
}

// // Filter based on rollup verdict
// function filterWithRollUpVerdict(nodes) {
//   return (nodes || [])
//     .map(node => {
//       let children = filterWithRollUpVerdict(node.children);
//       return children.length ? { ...node, children } : !node.match && node;
//     })
//     .filter(Boolean);
// }

// Filter based on rollup verdict
function filterWithRollUp(nodes) {
  return (nodes || [])
    .map(node => {
      let { children, ...childlessNode } = node;
      children = filterWithRollUp(children);
      return children.length
        ? { ...node, children }
        : !node.match && childlessNode;
    })
    .filter(Boolean);
}

export default GenericVpWithTreeNodeAndProperties;
