import React, { Component } from "react";
import { Table, Modal, Button, Tooltip } from "antd";
import ReactHtmlParser from "react-html-parser";
import {
  _pathVar,
  _reportServiceURIForSnapshotData,
  _failColorRed,
  _space,
  _colon,
  _vpFullDispStr,
  _imageDispStr,
  _slashFwd,
  _files,
  _mediaDir,
  _staticDir,
  _dot,
  isNotUndefinedAndNull,
  getVpTableScrollY,
  _exportModeYes
} from "./Constants";
import { ZoomInOutlined } from "@ant-design/icons";
import i18n from "../i18n";

const { Column } = Table;

class ImageVp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showImageVpFull: false
    };
  }
  render() {
    return this.getImageVpTable(this.props.clickedStepVpContent);
  }

  // Gets Image Vp table
  getImageVpTable = vpFileContent => {
    let isVpPass = vpFileContent.diff[0].match;
    // let temp = vpFileContent;
    let data = [];
    let item = {
      key: undefined,
      expected: undefined,
      actual: undefined,
      children: undefined
    };

    const vpImgSpanStyle = {
      margin: "0 auto",
      maxWidth: "100%",
      objectFit: "fit",
      display: "flex",
      height: this.state.showImageVpFull === false ? "25vh" : "70vh"
    };

    const params = new URLSearchParams(window.location.search);
    const xMoebReportPath = params.get(_pathVar);
    let snapShotUriExpected = "";
    let snapShotUriActual = "";

    let item1 = { expected: "", actual: "" };
    if (isNotUndefinedAndNull(vpFileContent.diff[0].OCRlines)) {
      isNotUndefinedAndNull(vpFileContent.diff[0]) &&
        vpFileContent.diff[0].OCRlines.diff.forEach(dif => {
          if (dif.match === true) {
            item1.expected = (
              <span>
                <span>{item1.expected}</span>
                <span>{dif.expected}</span>
                {_space}
              </span>
            );
            item1.actual = (
              <span>
                <span>{item1.actual}</span>
                <span>{dif.actual}</span>
                {_space}
              </span>
            );
          } else {
            isVpPass = false;
            item1.expected = (
              <span>
                <span>{item1.expected}</span>
                <span style={{ color: _failColorRed }}>{dif.expected}</span>
                {_space}
              </span>
            );
            item1.actual = (
              <span>
                <span>{item1.actual}</span>
                <span style={{ color: _failColorRed }}>{dif.actual}</span>
                {_space}
              </span>
            );
          }
        });
      item1.key = item1.actual + item1.expected;
      data.push(item1);
    }

    if (isNotUndefinedAndNull(vpFileContent.diff)) {
      snapShotUriExpected =
        window.location.origin +
        _reportServiceURIForSnapshotData +
        xMoebReportPath +
        _slashFwd +
        _files +
        _slashFwd +
        window.btoa(vpFileContent.diff[0].expected);

      snapShotUriActual =
        window.location.origin +
        _reportServiceURIForSnapshotData +
        xMoebReportPath +
        _slashFwd +
        _files +
        _slashFwd +
        window.btoa(vpFileContent.diff[0].actual);

      // only for export option
      if (
        process.env.REACT_APP_EXPORT_MODE === _exportModeYes &&
        process.env.NODE_ENV === "production"
      ) {
        // no need to go through btoa() for this
        snapShotUriExpected =
          _dot +
          _slashFwd +
          _staticDir +
          _slashFwd +
          _mediaDir +
          _slashFwd +
          vpFileContent.diff[0].expected;

        snapShotUriActual =
          _dot +
          _slashFwd +
          _staticDir +
          _slashFwd +
          _mediaDir +
          _slashFwd +
          vpFileContent.diff[0].actual;
      }
    }

    item.actual = (
      <span>
        <img
          style={vpImgSpanStyle}
          src={snapShotUriActual}
          alt="snapShotUriActual"
          // for development
          // src={"https://picsum.photos/id/200/800/1000"}
        ></img>
      </span>
    );
    item.expected = (
      <span>
        <img
          style={vpImgSpanStyle}
          src={snapShotUriExpected}
          alt="snapShotUriExpected"
          // for development
          // src={"https://picsum.photos/id/200/800/1000"}
        ></img>
      </span>
    );
    item.key =
      item.actual + item.expected + snapShotUriExpected + snapShotUriActual;
    data.push(item);

    let vptypeDispStr =
      _imageDispStr + _space + _vpFullDispStr + _colon + _space;
    let vpResultDispStr = isVpPass
      ? i18n.t("_vpDataAsExpDispStr")
      : i18n.t("_vpDataNotAsExpDispStr");

    if (this.state.showImageVpFull) {
      return (
        <Modal
          style={{ top: 20 }}
          width="85%"
          title={ReactHtmlParser(this.props.clickedStepDetails.description)}
          visible={this.state.showImageVpFull}
          // onOk={this.handleImageVpFullOk}
          onCancel={this.handleImageVpFullCancel}
          footer={null}
        >
          {this.getImgVpDetailsTable(
            isVpPass,
            data,
            vptypeDispStr,
            vpResultDispStr
          )}
        </Modal>
      );
    }

    return this.getImgVpDetailsTable(
      isVpPass,
      data,
      vptypeDispStr,
      vpResultDispStr
    );
  };

  // This is a method to return the table for img vp; separated as we show zoom for img vp
  getImgVpDetailsTable = (isVpPass, data, vptypeDispStr, vpResultDispStr) => {
    return (
      <React.Fragment>
        <div style={{ marginLeft: "15px", marginBottom: "5px" }}>
          <span style={{ fontWeight: "bold" }}>{vptypeDispStr}</span>
          <span>{vpResultDispStr}</span>
          {/* Dont show zoom btn in full screen mode */}
          {this.state.showImageVpFull === false && (
            <span style={{ float: "right" }}>
              <Tooltip title={i18n.t("_showImgVpFullScDispStr")}>
                <Button
                  size="small"
                  icon={<ZoomInOutlined />}
                  onClick={this.OnClickshowImageVpFull}
                >
                  {/* Show Full */}
                </Button>
              </Tooltip>
            </span>
          )}
        </div>
        <Table
          style={{
            // border: "1px solid blue",
            marginLeft: "15px"
            // margin: "0px 25px"
          }}
          dataSource={data}
          size="small"
          bordered
          scroll={
            this.state.showImageVpFull === true
              ? undefined
              : { y: getVpTableScrollY() }
          }
          pagination={false}
          className="ant-table-thead antd-table-row-hover-unset"
        >
          <Column
            className="details-table-row"
            title={
              isVpPass
                ? i18n.t("_expectedDispStr") +
                  _space +
                  i18n.t("_andDispStr") +
                  _space +
                  i18n.t("_actualDispStr")
                : i18n.t("_expectedDispStr")
            }
            dataIndex="expected"
            key="expectedListVP"
            // width={isVpPass === false ? 200 : undefined}
          />
          {isVpPass === false && (
            <Column
              className="details-table-row"
              title={i18n.t("_actualDispStr")}
              dataIndex="actual"
              key="actualListVP"
            />
          )}
        </Table>
      </React.Fragment>
    );
  };

  // This sets the state on click of button to open Image vp Moda
  OnClickshowImageVpFull = e => {
    this.setState({ showImageVpFull: true });
  };

  // This is called when clicked outside or pressed cancel on  Modal for ImageVp
  handleImageVpFullCancel = () => {
    this.setState({
      showImageVpFull: false
    });
  };
}

export default ImageVp;
