import React, { Component } from "react";
import TopStatusFragment from "./TopStatusFragment";
import { ProgressSpinner } from "primereact/progressspinner";
import {
  _pathVarInRestApi,
  _reportServiceURIForJsonData,
  _space,
  _reportTitle,
  _colon,
  _newReportDataDurationInSec,
  _exportReportPollInSec,
  _exportModeYes,
  _pdfExportType,
  _application_octet_stream,
  _application_pdf,
  _application_zip,
  isServerMode,
  setUnifiedReportVerInJson,
  _xmlExportType,
  _application_xml
} from "./Constants";
import axios from "axios";
import emptyDataForExport from "./empty.json";
import myTestData from "./aft2_withenvChanges.json";
import SummarySection from "./SummarySection";
import TestDetailsSection from "./TestDetailsSection";
import { notification } from "antd";
import Navbar from "./Navbar";
import i18n from "../i18n";

class MainPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reportDataFromApi: {},
      expReportState: false,
      reportExportResponse: undefined,
      axiosExportFailure: false
    };
  }

  componentDidMount() {
    this.getReportData();
    this.interval = setInterval(() => {
      this.getReportData();
      if (
        this.state.reportDataFromApi.summary !== undefined &&
        this.state.reportDataFromApi.summary.executionFinished === true
      ) {
        clearInterval(this.interval);
      }
    }, _newReportDataDurationInSec);
  }

  // This keeps getting called at a interval till we keep getting 202.
  // When we gte 200 we save the response and create another call to depening
  // on the information passed in that last response with 200
  getReportExportData = (format, filter) => {
    const params = new URLSearchParams(window.location.search);
    const xMoebReportPath = params.get(_pathVarInRestApi);
    let url1 =
      window.location.origin + _reportServiceURIForJsonData + xMoebReportPath;
    let custHeaders = {
      exportFormat: format,
      exportFilter: filter,
      "Accept-Language": window.navigator.language
    };
    if (window.navigator && window.navigator.msSaveOrOpenBlob) {
      // IE only
      custHeaders["Pragma"] = "no-cache";
    }

    axios({
      url: url1,
      method: "GET",
      headers: custHeaders
    })
      .then(response => {
        this.setState({ reportExportResponse: response });
      })
      .catch(error => {
        let msg = error.message;
        if (error.response && error.response.data) {
          msg = error.response.data.errorMessage;
        }
        notification.error({
          message: i18n.t("_errorDuringExport") + _colon + _space + msg,
          duration: 4
        });
        // enable button again
        this.setState({ expReportState: false });
        // make reportExportResponse undef again for new calls to export
        this.setState({ reportExportResponse: undefined });
        // mark some failure during Export
        this.setState({ axiosExportFailure: true });
      });
  };

  getReportData() {
    //The below condition sets the data from "myTestData"
    // for devlopment only. And this continues to work for production
    // environment as the var "process.env.NODE_ENV" in production
    // i.e. with "npm run build" will be "production" and
    // with "npm start", will be "development"
    if (process.env.NODE_ENV === "development")
      this.setState({ reportDataFromApi: myTestData });
    // console.log(window.location.search);
    //Reads details from URI
    const params = new URLSearchParams(window.location.search);
    const xMoebReportPath = params.get(_pathVarInRestApi);
    //window.location.origin returns example: http://10.115.96.38:7878 or http://localhost:7878
    let url =
      window.location.origin + _reportServiceURIForJsonData + xMoebReportPath;
    // Set Empty if _exportModeYes(used for Export option),
    // otherwise make REST call to get report json data
    if (process.env.REACT_APP_EXPORT_MODE === _exportModeYes) {
      this.setState({ reportDataFromApi: emptyDataForExport });
    } else {
      axios
        .get(url)
        .then(response => {
          this.setState({ reportDataFromApi: response.data });
        })
        .catch(error => {
          if (error.response) {
            // Check only for OTS/RTAS,
            if (error.response.status === 401) {
              this.redirectToLoginForServer();
            }
          }
        });
    }
  }

  redirectToLoginForServer() {
    if (isServerMode()) {
      const authUrl = "/sso/login?origin=@location";
      const origin =
        window.location.pathname +
        window.location.search +
        window.location.hash;
      window.location.href = authUrl.replace(
        "@location",
        encodeURIComponent(origin)
      );
    }
  }

  getHeaderAcceptParam(format, isIE) {
    let headerAcceptParam = _application_octet_stream;
    if (isIE) {
      headerAcceptParam = _application_zip;
    }
    if (format === _pdfExportType) {
      headerAcceptParam = _application_pdf;
    } else if (format === _xmlExportType) {
      headerAcceptParam = _application_xml;
    }
    return headerAcceptParam;
  }

  // method called on click of export button on the report,
  //which essentially takes care of poilling till the
  // exported zip(gets to know by a 200 status, along with the loation info)
  // is available. It then downloads the zip using
  //another GET call to the location mentioned.
  exportToFormat = async (format, filter) => {
    this.setState({ expReportState: true });
    await this.getReportExportData(format, filter);
    this.reportExportInterval = setInterval(() => {
      // if the state "axiosExportFailure" is set to true, clearing
      // the interval as it could be some network/ any other
      // error etc, so the call to REST is finished
      if (this.state.axiosExportFailure === true) {
        clearInterval(this.reportExportInterval);
      }
      this.getReportExportData(format, filter);
      if (
        this.state.reportExportResponse !== undefined &&
        this.state.reportExportResponse.status === 200
      ) {
        clearInterval(this.reportExportInterval);
        axios({
          url:
            window.location.origin +
            this.state.reportExportResponse.headers.location,
          method: "GET",
          responseType: "blob", // important
          headers: {
            Accept: this.getHeaderAcceptParam(format, false)
          }
        })
          .then(response => {
            let filename = response.headers["content-disposition"].split(
              "filename="
            )[1];
            if (window.navigator && window.navigator.msSaveOrOpenBlob) {
              // IE variant
              window.navigator.msSaveOrOpenBlob(
                new Blob([response.data], {
                  type: this.getHeaderAcceptParam(format, true)
                }),
                filename
              );
            } else {
              const url = window.URL.createObjectURL(new Blob([response.data]));
              const link = document.createElement("a");
              link.href = url;
              link.setAttribute("download", filename); //the name with which it'll be dowloaded
              document.body.appendChild(link);
              link.click();
            }
            // enable button again
            this.setState({ expReportState: false });
            // make reportExportResponse undef again for new calls to export
            this.setState({ reportExportResponse: undefined });
          })
          .catch(error => {
            let msg = error.message;
            if (error.response && error.response.data) {
              msg = error.response.data.errorMessage;
            }
            notification.error({
              message: i18n.t("_errorDuringExport") + _colon + _space + msg,
              duration: 4
            });
            // enable button again
            this.setState({ expReportState: false });
            // make reportExportResponse undef again for new calls to export
            this.setState({ reportExportResponse: undefined });
            // mark some failure during Export
            this.setState({ axiosExportFailure: true });
          });
      }
    }, _exportReportPollInSec);
  };

  render() {
    if (this.state.reportDataFromApi.summary === undefined) {
      document.title =
        _reportTitle + _colon + _space + i18n.t("_loadingDispStr");
      return (
        <ProgressSpinner
          animationDuration="2s"
          style={{ display: "flex", justifyContent: "center" }}
        />
      );
    }
    document.title =
      _reportTitle +
      _colon +
      _space +
      this.state.reportDataFromApi.summary.name;
    // Set the version specified in Summary's version field to a var
    if (this.state.reportDataFromApi.summary.version) {
      setUnifiedReportVerInJson(this.state.reportDataFromApi.summary.version);
    }
    return (
      <div role="main">
        <Navbar
          summaryData={this.state.reportDataFromApi.summary}
          exportToFormatFuncProp={this.exportToFormat}
          expReportProp={this.state.expReportState}
        />
        <TopStatusFragment
          summaryData={this.state.reportDataFromApi.summary}
          expReportProp={this.state.expReportState}
        />
        <SummarySection summaryData={this.state.reportDataFromApi.summary} />
        <TestDetailsSection
          summaryEnvironment={this.state.reportDataFromApi.summary.environment}
          testDetailsData={this.state.reportDataFromApi.testDetails}
          summaryData={this.state.reportDataFromApi.summary}
        />
      </div>
    );
  }
}

export default MainPage;
