import React, { Component } from "react";
import {
  Row,
  Col,
  Tree,
  Table,
  Modal,
  Button,
  Alert,
  Divider,
  Avatar,
  Empty,
  Typography
} from "antd";
import { CheckCircleOutlined, CloseCircleOutlined } from "@ant-design/icons";
import ReactHtmlParser from "react-html-parser";
import {
  _passStrInJson,
  _pathVar,
  _reportServiceURIForSnapshotData,
  _reportServiceURIForVP,
  convertTimeStampToDateStr,
  _space,
  _msDispStr,
  _thinkTimeDispStr,
  _colon,
  getDuration,
  _vpTypeText,
  _vpTypeProperty,
  _vpTypeList,
  _vpTypeImage,
  _vpTypeTree,
  _vpTypeTable,
  _propertyDispStr,
  _treeDispStr,
  _datasetDispStr,
  _iterSummDispStr,
  _iterationStrInJson,
  _bracesOpen,
  _bracesClose,
  _passColorGreenLighter,
  _failColorRedLighter,
  _vpTypeState,
  _slashFwd,
  _files,
  _vpTypeMenu,
  _menuDispStr,
  isNotUndefinedAndNull,
  _dot,
  _staticDir,
  _mediaDir,
  _stateDispStr,
  _vp_version_1,
  getVpTableScrollY,
  _exportModeYes,
  _stepsVirtualTreeCount
} from "./Constants";
import axios from "axios";
import noImg from "../resources/images/noimg.png";
import GenericVpWithTreeNodeAndProperties from "./GenericVpWithTreeNodeAndProperties";
import TextVp from "./TextVp";
import ListVp from "./ListVp";
import PropertyVp from "./PropertyVp";
import TableVp from "./TableVp";
import ImageVp from "./ImageVp";
import StateVp from "./StateVp";
import NavigateToTest from "./NavigateToTest";
import {
  UpSquareOutlined,
  DownSquareOutlined,
  CheckCircleFilled,
  CloseCircleFilled
} from "@ant-design/icons";
import i18n from "../i18n";

const { TreeNode } = Tree;
const { Column } = Table;

class TestTabContents extends Component {
  constructor(props) {
    super(props);
    this.stepstreeSpan = React.createRef();
    this.state = {
      visibleSnapshot: false,
      test: this.props.test,
      //making 1st event as the selected, the string "1" is given to the node as id
      selectedNodeKey: ["1"],
      // total count of flat nodes(flat for call scripts and iteration present)
      totalNodesCount: 0,

      // For setting the default clicked step: if the iteration itself is not
      // present but the other test level details are, then we set an
      // empty {} for default clicked step. But if iteration is present then
      // we decide based on dataset presence
      clickedStepDetails:
        this.props.test.iteration === undefined
          ? {}
          : this.props.test.iteration.length >= 1 &&
            this.props.test.iteration[0].dataset === undefined
          ? this.props.test.iteration[0].events[0]
          : this.props.test.iteration[0],
      showPropVpDiffOnly: false,
      showListVpDiffOnly: false,
      showImageVpFull: false,
      showTableVpFull: false,
      clickedStepVpContent: undefined,
      clickedStepVpType: undefined
    };
  }

  componentWillReceiveProps() {
    this.setState({ test: this.props.test });
    let node = { count: 0 };
    this.countNodes(this.props.test, node);
    this.setState({ totalNodesCount: node.count });
  }

  getEventStepStatusIcon = event => {
    if (event.status === _passStrInJson) {
      return <CheckCircleOutlined className="pass-color" />;
    } else {
      return <CloseCircleOutlined className="fail-color" />;
    }
  };

  // Gets steps navigation buttons
  getTestTabNavButtons = () => {
    return (
      <React.Fragment>
        <Button
          className="test-tab-content-nav-btn"
          icon={<UpSquareOutlined className="nav-btn-icon" />}
          disabled={this.state.selectedNodeKey[0] === "1"}
          onClick={this.SelectPreviousEvent}
        />
        <Button
          className="test-tab-content-nav-btn"
          icon={<DownSquareOutlined className="nav-btn-icon" />}
          disabled={
            this.state.totalNodesCount ===
            parseInt(this.state.selectedNodeKey[0])
          }
          onClick={this.SelectNextEvent}
        />
      </React.Fragment>
    );
  };

  // This opens up the Modal for the snapshot
  getSnapshotDialog = snapShotUri => {
    // const dialogHeaderStyle = {
    //   borderLeftColor:
    //     this.state.clickedStepDetails.status === _passStrInJson
    //       ? _passColorGreen
    //       : _failColorRed
    // };

    // doesn't work wth simple classname(works only with inline)
    const modalBodyStyle = { paddingTop: "8px", paddingBottom: "8px" };

    return (
      <div>
        <Modal
          role="dialog"
          className="modal-carousel-or-imgzoom"
          bodyStyle={modalBodyStyle}
          width={"65%"}
          title={
            <span
              className={
                "step-snapshot-in-dialog-title" +
                (this.state.clickedStepDetails.status === _passStrInJson
                  ? " pass-left-border"
                  : " fail-left-border")
              }
              // style={dialogHeaderStyle}
            >
              {ReactHtmlParser(this.state.clickedStepDetails.description)}
            </span>
          }
          visible={this.state.visibleSnapshot}
          onCancel={this.onCloseSnapshotImg}
          footer={null}
        >
          <img
            src={snapShotUri}
            alt={snapShotUri}
            className="testtab-img-in-modal"
          ></img>
        </Modal>
      </div>
    );
  };

  // This is to close the Opened snapshot Modal
  onCloseSnapshotImg = e => {
    this.setState({ visibleSnapshot: false });
  };

  // This is to set the state when the click to a Image happens to open it up in Modal
  openSnapshotImg = (e, clickedItem) => {
    this.setState({ visibleSnapshot: true });
    return <img src={e.currentTarget.currentSrc} alt="snapshot"></img>;
  };

  // This makes the REST call to get vp details and set the proper state
  makeCallAndSetVp = fullEvent => {
    //return incase this happens to be iteratin type of event which doesn't have any additional info
    if (fullEvent.additionalDetails === undefined) return;

    // clearing states for next VP render(ususally when 2 or more consequtive Vps are present)
    this.setState({ clickedStepVpType: undefined, clickedStepVpContent: undefined });

    let vpObj = fullEvent.additionalDetails.vp;
    if (vpObj !== undefined && vpObj !== null) {
      // vp present
      const params = new URLSearchParams(window.location.search);
      const xMoebReportPath = params.get(_pathVar);
      let vpFile = "";
      let vpType = "";
      let vpFileUri = "";

      // let vpObj = vpObjArr[0];
      vpFile = vpObj.file;
      vpType = vpObj.type;
      vpFileUri =
        // Used for production
        window.location.origin +
        // Used for developement
        // "http://localhost:7878" +
        _reportServiceURIForVP +
        xMoebReportPath +
        _slashFwd +
        _files +
        _slashFwd +
        // using this the vp file name can contain utf-8 chars. The backend does a url_decode().
        // Doing only a btoa() has a "/" in the encoded, hence url_encode is required
        base64url_encode(vpFile)

      // only for export
      if (process.env.REACT_APP_EXPORT_MODE === _exportModeYes) {
        setTimeout(() => {
          this.setState({ clickedStepVpContent: vpObj.file });
          this.setState({ clickedStepVpType: vpObj.type });
        });
      }

      // Checking if its not for export option and its a production
      // build, then continue to make REST calls for VP data
      else if (
        process.env.REACT_APP_EXPORT_MODE !== _exportModeYes &&
        process.env.NODE_ENV === "production"
      ) {
        axios.get(vpFileUri).then(response => {
          this.setState({ clickedStepVpContent: response.data });
          this.setState({ clickedStepVpType: vpType });
        });
      }
      // Commented the below else block. Enable this only to devlop/debug
      // the Traditional FT VPs without making the REST calls for the VP
      // json data by following thr comment inside the block
      /*
      else {
        // This condition is used only of dev/debugging
        // Set the VP json data in the variable "anyVpDataToTest" and
        // set the supported VPType in "clickedStepVpType" in the setState() as "text/list etc"
        // Kept the "anyVpDataToTest" as undef so in dev we continue to render other details of step if its undefined
        let anyVpDataToTest = undefined;
        if (anyVpDataToTest !== undefined) {
          this.setState({ clickedStepVpType: "EnterTypeOfVpSupported" });
          this.setState({ clickedStepVpContent: anyVpDataToTest });
        }
      }
      */
    } else {
      //condition to not set undefined always to stop unnecessary re-render
      if (isNotUndefinedAndNull(this.state.clickedStepVpContent)) {
        this.setState({ clickedStepVpContent: undefined });
        this.setState({ clickedStepVpType: undefined });
      }
    }
  };

  // This is called when a click to a node happens
  onNodeSelected = (selectedKeys, info) => {
    this.setState({ clickedStepDetails: info.node.props.fullEvent });
    // this.setState({ selectedNodeKey: [info.node.props.fullEvent.eventUid] });
    this.setState({ selectedNodeKey: [info.node.props.eventKey] });
    this.makeCallAndSetVp(info.node.props.fullEvent);
  };

  // This method ensures to show the smaller snapshot for a selected/Clicked step
  getSnapshotInTab(snapShotUri) {
    let step = this.state.clickedStepDetails;
    if (step === undefined) return;
    // render no img icon when screenshot is not present
    if (step.snapshot === undefined || step.snapshot === null) {
      return (
        <React.Fragment>
          <span>{i18n.t("_screenshotDisStr")}</span>
          <div className="test-tab-content-snapshot-in-tab-noimg">
            {/* <Icon type="file-excel" /> */}
            <Avatar src={noImg} alt="NoImage" />
          </div>
        </React.Fragment>
      );
    }

    return (
      <div>
        <div>{i18n.t("_screenshotDisStr")}</div>
        {isNotUndefinedAndNull(step.snapshot) && (
          // have put the img inside button for accessibility
          <button
            value="buttonWithStepImg"
            aria-label="buttonWithStepImg"
            className="test-tab-content-button-for-snapshot-in-tab"
            onClick={e => this.openSnapshotImg(e, step)}
          >
            <img
              className="test-tab-content-snapshot-in-tab"
              src={snapShotUri}
              alt={snapShotUri}
              onClick={e => this.openSnapshotImg(e, step)}
            ></img>
          </button>
        )}
      </div>
    );
  }

  getStepDescSectionAlert = step => {
    const { Paragraph } = Typography;

    if (step === undefined) return;
    if (
      isNotUndefinedAndNull(step.additionalDetails) &&
      isNotUndefinedAndNull(step.additionalDetails.alertDescriptor)
    )
      return (
        <Alert
          className="below-desc-alert"
          style={{
            backgroundColor:
              step.additionalDetails.alertDescriptor.status === _passStrInJson
                ? _passColorGreenLighter
                : _failColorRedLighter
          }}
          message={
            <Paragraph
              ellipsis={{
                rows: 1,
                expandable: true,
                symbol: (
                  <span className="alert-expand-text">
                    {i18n.t("_stepDescAlertExpandLink")}
                  </span>
                )
              }}
            >
              {ReactHtmlParser(
                step.additionalDetails.alertDescriptor.verdictMessage
              )}
            </Paragraph>
          }
          // not using type as we need diff color
          // type={}
          // showIcon
        />
      );
  };

  // This gets the icon, description that shows up when a step is clicked/selected
  getStepDescriptionWithStatus = () => {
    const { Paragraph } = Typography;

    let step = this.state.clickedStepDetails;
    if (step === undefined) return;
    return (
      <React.Fragment>
        <Row className="step-desc-n-status-row">
          <div className="step-status">
            <span>
              {step.status === _passStrInJson ? (
                <CheckCircleFilled className="step-status-icon pass-color" />
              ) : (
                <CloseCircleFilled className="step-status-icon fail-color" />
              )}
            </span>
            <span
              className={
                "test-tab-content-desc-sec-status-str" +
                (step.status === _passStrInJson ? " pass-color" : " fail-color")
              }
            >
              {step.status === _passStrInJson
                ? i18n.t("_passDispStr")
                : i18n.t("_failDispStr")}
            </span>
          </div>
          <div className="desc-sec-parent-div">
            <span className="test-tab-content-desc-sec-desc">
              <Paragraph
                ellipsis={{
                  rows: 2,
                  expandable: true,
                  symbol: (
                    <span className="desc-expand-text">
                      {i18n.t("_stepDescAlertExpandLink")}
                    </span>
                  )
                }}
              >
                {ReactHtmlParser(step.description)}
              </Paragraph>
            </span>
          </div>
        </Row>
        <Row>{this.getStepDescSectionAlert(step)}</Row>
      </React.Fragment>
    );
  };

  // Gete the iteration info when clciked on a iteration node in tree
  getIterationInfo = step => {
    let temp = {};
    temp = step;
    if (temp === undefined) return;

    let datasetDispStr =
      _datasetDispStr + _colon + _space + step.dataset[0].dataSetName;
    let iterDetailsDispStr = _iterSummDispStr;

    // looping though the data and adding unique key, avoids warning
    step.dataset[0].data.forEach((item, i) => {
      item.key = i;
    });

    return (
      <React.Fragment>
        <div className="iter-summ-container">
          <span className="iter-summ-heading">{iterDetailsDispStr}</span>
          <div className="itr-step-counter-container">
            <span className="itr-step-counter-span">
              {i18n.t("_totalStepsDispStr") + _colon + _space}
              <span className="itr-step-counter-val-span primary-color">
                {step.summary.totalSteps}
              </span>
            </span>
            <span className="itr-step-counter-span">
              {i18n.t("_passedStepsDispStr") + _colon + _space}
              <span className="itr-step-counter-val-span pass-color">
                {step.summary.passedSteps}
              </span>
            </span>
            <span className="itr-step-counter-span">
              {i18n.t("_failedStepsDispStr") + _colon + _space}
              <span className="itr-step-counter-val-span fail-color">
                {step.summary.failedSteps}
              </span>
            </span>
            <span className="itr-step-counter-span">
              {i18n.t("_durationDispStr") + _colon + _space}
              <span className="itr-step-counter-val-span grey-color-6">
                {getDuration(step.executionTime)}
              </span>
            </span>
          </div>
        </div>
        <div className="itr-dataset-disp-str-container">
          <span className="itr-dataset-disp-str">{datasetDispStr} </span>
        </div>
        <Table
          dataSource={step.dataset[0].data}
          size="small"
          bordered
          scroll={{ y: getVpTableScrollY() }}
          pagination={false}
          className="ant-table-thead antd-table-row-hover-unset itr-details-table"
        >
          {step.dataset[0].columnNames.map(col => {
            return (
              <Column
                className="details-table-row"
                title={col}
                dataIndex={col}
                key={col}
              />
            );
          })}
        </Table>
      </React.Fragment>
    );
  };

  // Gete the Step info when clciked on a step node in tree
  getStepInfoTable = () => {
    let step = this.state.clickedStepDetails;
    if (step === undefined) return;
    //conditions to render VPs
    if (isNotUndefinedAndNull(this.state.clickedStepVpContent)) {
      if (this.state.clickedStepVpType === _vpTypeText) {
        return (
          <TextVp
            clickedStepVpContent={this.state.clickedStepVpContent}
            // clickedStepDetails={this.state.clickedStepDetails}
          />
        );
        //this.getTextVpTable(this.state.clickedStepVpContent);
      } else if (this.state.clickedStepVpType === _vpTypeProperty) {
        // if the version is undefined which means its a vp json from
        // release 10.1, we render the old look of property vp same as
        // in 10.1, now with 10.1.1 a version property in vp json is
        // introduced. So checking if the version is present, we
        // render the new look of property vp
        if (this.state.clickedStepVpContent.version === undefined) {
          return (
            <PropertyVp
              clickedStepVpContent={this.state.clickedStepVpContent}
              clickedStepDetails={this.state.clickedStepDetails}
            />
          );
          //this.getPropertyVpTable(this.state.clickedStepVpContent);
        } else if (
          // Render new style VP for Version 1 and above
          parseInt(this.state.clickedStepVpContent.version) >= _vp_version_1
        ) {
          return (
            <GenericVpWithTreeNodeAndProperties
              clickedStepVpContent={this.state.clickedStepVpContent}
              clickedStepDetails={this.state.clickedStepDetails}
              vpTypeForDisplay={_propertyDispStr}
            />
          );
        }
      } else if (this.state.clickedStepVpType === _vpTypeList) {
        return (
          <ListVp
            clickedStepVpContent={this.state.clickedStepVpContent}
            // clickedStepDetails={this.state.clickedStepDetails}
          />
        );
        //this.getListVpTable(this.state.clickedStepVpContent);
      } else if (this.state.clickedStepVpType === _vpTypeImage) {
        return (
          <ImageVp
            clickedStepVpContent={this.state.clickedStepVpContent}
            clickedStepDetails={this.state.clickedStepDetails}
          />
        );
        //this.getImageVpTable(this.state.clickedStepVpContent);
      } else if (this.state.clickedStepVpType === _vpTypeTree) {
        return (
          // removed the use of old style tree vp and now using
          // new Generic Tree with props style vp
          <GenericVpWithTreeNodeAndProperties
            clickedStepVpContent={this.state.clickedStepVpContent}
            clickedStepDetails={this.state.clickedStepDetails}
            vpTypeForDisplay={_treeDispStr}
          />
        );
        //this.getTreeVpTable(this.state.clickedStepVpContent);
      } else if (this.state.clickedStepVpType === _vpTypeMenu) {
        return (
          // removed the use of old style menu vp and now using
          // new Generic Tree with props style vp
          <GenericVpWithTreeNodeAndProperties
            clickedStepVpContent={this.state.clickedStepVpContent}
            clickedStepDetails={this.state.clickedStepDetails}
            vpTypeForDisplay={_menuDispStr}
          />
        ); //this.getMenuVpTable(this.state.clickedStepVpContent);
      } else if (this.state.clickedStepVpType === _vpTypeTable) {
        return (
          <TableVp
            clickedStepVpContent={this.state.clickedStepVpContent}
            clickedStepDetails={this.state.clickedStepDetails}
          />
        );
        //this.getTableVpTable(this.state.clickedStepVpContent);
      } else if (this.state.clickedStepVpType === _vpTypeState) {
        // if the version is undefined which means its a vp json from
        // release 10.1, we render the old look of state vp same as
        // in 10.1, now with 10.1.1 a version property in vp json is
        // introduced. So checking if the version is present, we
        // render the new look of state vp
        if (this.state.clickedStepVpContent.version === undefined) {
          return (
            <StateVp
              clickedStepVpContent={this.state.clickedStepVpContent}
              clickedStepDetails={this.state.clickedStepDetails}
            />
          );
          //this.getStateVpTable(this.state.clickedStepVpContent);
        } else if (
          // Render new style VP for Version 1 and above
          parseInt(this.state.clickedStepVpContent.version) >= _vp_version_1
        ) {
          return (
            <GenericVpWithTreeNodeAndProperties
              clickedStepVpContent={this.state.clickedStepVpContent}
              clickedStepDetails={this.state.clickedStepDetails}
              vpTypeForDisplay={_stateDispStr}
              vpType={this.state.clickedStepVpType}
            />
          );
        }
      }
      //will be removed once all vps are done
      return <div>{JSON.stringify(this.state.clickedStepVpContent)}</div>;
    }
    // if its iteration and dataset is defined then only show dataset table
    else if (
      step.type === _iterationStrInJson &&
      isNotUndefinedAndNull(step.dataset)
    ) {
      return this.getIterationInfo(step);
    }

    let data = [];
    let item = {
      property: i18n.t("_summTabStartTimeDispStr"),
      value: convertTimeStampToDateStr(step.startTimestamp)
    };
    item.key = item.property + item.value;
    // if startTimestamp is ZERO, dont show startTimestamp
    if (step.startTimestamp !== 0) data.push(item);

    item = {
      property: i18n.t("_summTabEndTimeDispStr"),
      value: convertTimeStampToDateStr(step.endTimestamp)
    };
    item.key = item.property + item.value;
    // if executionTime is ZERO, dont show EndTime also, as this is
    // mostyly going to be same as StartTime. We can change this depending on requirement
    if (step.executionTime !== 0) data.push(item);

    item = {
      property: i18n.t("_netEndToEndRespTimeDispStr"),
      value: step.executionTime + _space + _msDispStr
    };
    item.key = item.property + item.value;
    // if executionTime is ZERO, dont show ExecutionTime
    if (step.executionTime !== 0) data.push(item);

    let thinkVal = "";
    if (
      isNotUndefinedAndNull(step.additionalDetails) &&
      isNotUndefinedAndNull(step.additionalDetails.thinktime)
    ) {
      let detailThinkTime = step.additionalDetails.thinktime;
      thinkVal = ReactHtmlParser(detailThinkTime.description);
      if (isNotUndefinedAndNull(detailThinkTime.verdictMessage)) {
        thinkVal = ReactHtmlParser(
          detailThinkTime.description +
            _space +
            _bracesOpen +
            detailThinkTime.verdictMessage +
            _bracesClose
        );
      }
      item = {
        key: _thinkTimeDispStr + thinkVal,
        property: _thinkTimeDispStr,
        value: thinkVal
      };
      if (thinkVal !== "") data.push(item);
    }

    // logic to show any other detail for a clicked step in addition detail
    // with key as "property" and value containing "propertyList[]" of {key, value, showInUI}
    if (
      isNotUndefinedAndNull(step.additionalDetails) &&
      isNotUndefinedAndNull(step.additionalDetails.properties)
    ) {
      step.additionalDetails.properties.forEach((propObj, propObjIndx) => {
        if (propObj.showInUI === true) {
          data.push({
            // making the index as key to have unique key for row
            key: propObjIndx,
            property: propObj.key,
            value: ReactHtmlParser(propObj.value)
          });
        }
      });
    }

    //logic for alertDescriptor
    if (
      isNotUndefinedAndNull(step.additionalDetails) &&
      isNotUndefinedAndNull(step.additionalDetails.alertDescriptor)
    ) {
      let alertDescObj = step.additionalDetails.alertDescriptor;
      let verificationVal = undefined;
      let verificationValStatus = undefined;
      verificationVal = ReactHtmlParser(alertDescObj.verdictMessage);
      verificationValStatus = alertDescObj.status;

      item = {
        key: verificationVal,
        property: i18n.t("_verdictMessageDispStr"),
        value: (
          <span
            className={
              verificationValStatus === _passStrInJson
                ? "pass-light-bgcolor"
                : "fail-light-bgcolor"
            }
          >
            {verificationVal}
          </span>
        )
      };
      if (isNotUndefinedAndNull(verificationVal)) data.push(item);
    }

    return (
      <React.Fragment>
        <Table
          style={{
            marginLeft: "15px"
          }}
          dataSource={data}
          size="small"
          bordered
          scroll={{ y: getVpTableScrollY() }}
          pagination={false}
          className="ant-table-thead antd-table-row-hover-unset"
        >
          <Column
            className="details-table-row"
            title="Property"
            dataIndex="property"
            key="propertyStepInfo"
            width={210}
          />
          <Column
            className="details-table-row"
            title="Value"
            dataIndex="value"
            key="valueStepInfo"
          />
        </Table>
      </React.Fragment>
    );
  };

  // This is to get the dynamic height of steps section
  // to set the size when steps tree's virtual rendering mode is ON
  getStepsTreeSpanHeightForVirtualMode = () => {
    let ht =
      this.stepstreeSpan &&
      this.stepstreeSpan.current &&
      this.stepstreeSpan.current.clientHeight;
    if (ht > 0) {
      return ht - (ht * 2) / 100;
    }
    return;
  };

  // This gets the Tree that shows steps
  getStepsTree = test => {
    let tree1 = test;
    let currNode = { count: 0 };
    // This is set to null to NOT have the virtual Tree;
    // Or a number to set the height of the tree when virtual mode is ON
    let virtualTreeHeight = null;
    if (this.renderVirtualScrollForStepTree()) {
      virtualTreeHeight = this.getStepsTreeSpanHeightForVirtualMode();
    }
    return (
      <React.Fragment>
        <Tree
          aria-label="steps tree"
          aria-hidden={true}
          tabIndex={-1}
          virtual={true}
          height={virtualTreeHeight}
          onSelect={this.onNodeSelected}
          // onLoad={this.onLoadNode}
          selectedKeys={this.state.selectedNodeKey}
          defaultSelectedKeys={this.state.sNode}
          defaultExpandAll
          selectable
          showIcon
          showLine={{ showLeafIcon: false }}
          className="steps-tree-comp"
        >
          {tree1.iteration === undefined
            ? tree1.events.map(ev => {
                return ev.events !== undefined ? (
                  this.parseNode2(ev, currNode)
                ) : (
                  <TreeNode
                    fullEvent={ev}
                    icon={this.getEventStepStatusIcon(ev)}
                    title={
                      <React.Fragment>
                        <NavigateToTest event={ev} test={this.props.test} />
                        {ReactHtmlParser(ev.description)}
                      </React.Fragment>
                    }
                    key={++currNode.count}
                  />
                );
              })
            : tree1.iteration.length === 1 &&
              tree1.iteration[0].dataset === undefined
            ? tree1.iteration[0].events.map(ev => {
                return ev.callScript !== undefined ? (
                  this.parseNode2(ev, currNode)
                ) : (
                  <TreeNode
                    fullEvent={ev}
                    icon={this.getEventStepStatusIcon(ev)}
                    title={
                      <React.Fragment>
                        <NavigateToTest event={ev} test={this.props.test} />
                        {ReactHtmlParser(ev.description)}
                      </React.Fragment>
                    }
                    key={(currNode.count = currNode.count + 1)}
                  />
                );
              })
            : tree1.iteration.map((iter, index) => {
                return (
                  <TreeNode
                    fullEvent={iter}
                    icon={this.getEventStepStatusIcon(iter)}
                    // don't add navigate btn for iter
                    title={ReactHtmlParser(iter.description)}
                    key={(currNode.count = currNode.count + 1)}
                  >
                    {iter.events.map(ev => {
                      return ev.callScript !== undefined ? (
                        this.parseNode2(ev, currNode)
                      ) : (
                        <TreeNode
                          fullEvent={ev}
                          icon={this.getEventStepStatusIcon(ev)}
                          title={
                            <React.Fragment>
                              <NavigateToTest
                                event={ev}
                                test={this.props.test}
                              />
                              {ReactHtmlParser(ev.description)}
                            </React.Fragment>
                          }
                          key={(currNode.count = currNode.count + 1)}
                        />
                      );
                    })}
                  </TreeNode>
                );
              })}
        </Tree>
      </React.Fragment>
    );
  };

  // Utitlity methos to count nodes for with DFS in tree
  countNodes = (event, node) => {
    // the event param is the test obj first time, then we loop
    // through DFS to get the count of all the nodes, each item
    // in the tree is a node for UI, ie the iteration node also
    if (event.iteration !== undefined) {
      event.iteration.forEach(iter => {
        node.count = node.count + 1;
        this.countNodes(iter, node);
      });
    }
    // else if (event.callScript !== undefined) {
    //   node.count = node.count + 1;
    //   this.countNodes(event.callScript.iteration, node);
    // }
    else if (event.events !== undefined) {
      event.events.forEach(ev => {
        if (ev.events === undefined) {
          node.count = node.count + 1;
        } else if (ev.callScript !== undefined) {
          this.countNodes(ev.callScript.iteration, node);
        } else {
          node.count = node.count + 1;
          this.countNodes(ev, node);
        }
      });
    }
  };

  parseNode2(events, currNode) {
    if (events.callScript !== undefined) {
      return (
        <TreeNode
          fullEvent={events}
          icon={this.getEventStepStatusIcon(events)}
          title={
            <React.Fragment>
              <NavigateToTest event={events} test={this.props.test} />
              {ReactHtmlParser(events.description)}
            </React.Fragment>
          }
          key={++currNode.count}
        >
          {events.callScript.iteration.length === 1 &&
            events.callScript.iteration[0].dataset === undefined &&
            events.callScript.iteration[0].events.map(ev => {
              if (ev.callScript === undefined) {
                return (
                  <TreeNode
                    fullEvent={ev}
                    icon={this.getEventStepStatusIcon(ev)}
                    title={
                      <React.Fragment>
                        <NavigateToTest event={ev} test={this.props.test} />
                        {ReactHtmlParser(ev.description)}
                      </React.Fragment>
                    }
                    key={++currNode.count}
                  ></TreeNode>
                );
              } else {
                return this.parseNode2(ev, currNode);
              }
            })}
          {/*Render the iteration node when the iteration count is more than 1 OR 
            when equal to 1 and dataset is present(NOT undefined)*/}
          {events.callScript.iteration.length >= 1 &&
            events.callScript.iteration[0].dataset !== undefined &&
            events.callScript.iteration.map(iterIth => {
              return (
                <TreeNode
                  fullEvent={iterIth}
                  icon={this.getEventStepStatusIcon(iterIth)}
                  // don't add the navigate btn for iter
                  title={ReactHtmlParser(iterIth.description)}
                  key={++currNode.count}
                >
                  {iterIth.events.map(ev => {
                    if (ev.callScript === undefined) {
                      return (
                        <TreeNode
                          fullEvent={ev}
                          icon={this.getEventStepStatusIcon(ev)}
                          title={
                            <React.Fragment>
                              <NavigateToTest
                                event={ev}
                                test={this.props.test}
                              />
                              {ReactHtmlParser(ev.description)}
                            </React.Fragment>
                          }
                          key={++currNode.count}
                        ></TreeNode>
                      );
                    } else {
                      return this.parseNode2(ev, currNode);
                    }
                  })}
                </TreeNode>
              );
            })}
        </TreeNode>
      );
    }
    return (
      <TreeNode
        fullEvent={events}
        icon={this.getEventStepStatusIcon(events)}
        title={
          <React.Fragment>
            <NavigateToTest event={events} test={this.props.test} />
            {ReactHtmlParser(events.description)}
          </React.Fragment>
        }
        key={++currNode.count}
      >
        {events.events.map(ev => {
          return ev.callScript === undefined ? (
            <TreeNode
              fullEvent={ev}
              icon={this.getEventStepStatusIcon(ev)}
              title={
                <React.Fragment>
                  <NavigateToTest event={ev} test={this.props.test} />
                  {ReactHtmlParser(ev.description)}
                </React.Fragment>
              }
              key={++currNode.count}
            />
          ) : (
            this.parseNode2(ev, currNode)
          );
        })}
      </TreeNode>
    );
  }

  // Utility to adjust scroll bar for selected step when Nav buttons are used
  bringStepIntoViewOnDownNavBtn = curr => {
    const currScrollPos = this.stepstreeSpan.current.scrollTop;
    const stepTreeSectionHeight = this.stepstreeSpan.current.clientHeight;
    const aStepTreeNodeHeight = 38;
    let totalVisibleSteps = stepTreeSectionHeight / aStepTreeNodeHeight;
    let invisibleNodes =
      currScrollPos / (stepTreeSectionHeight / totalVisibleSteps);
    let currentStepId = parseInt(curr[0]);
    let currSelectedNodePosition = currentStepId - Math.ceil(invisibleNodes);
    if (
      currSelectedNodePosition * aStepTreeNodeHeight + aStepTreeNodeHeight >
      stepTreeSectionHeight
    ) {
      this.stepstreeSpan.current.scrollTo(
        0,
        currScrollPos + aStepTreeNodeHeight
      );
    }
  };

  // Utility to adjust scroll bar for selected step when Nav buttons are used
  bringStepIntoViewOnUpNavBtn = curr => {
    const currScrollPos = this.stepstreeSpan.current.scrollTop;
    const aStepTreeNodeHeight = 38;
    let invisibleNodes = currScrollPos / aStepTreeNodeHeight;
    invisibleNodes = Math.ceil(invisibleNodes);
    let currentStepId = parseInt(curr[0]);
    let currSelectedNodePosition = currentStepId - Math.round(invisibleNodes);
    if (
      Math.floor(currSelectedNodePosition) * aStepTreeNodeHeight -
        aStepTreeNodeHeight <=
      0
    ) {
      this.stepstreeSpan.current.scrollTo(
        0,
        currScrollPos - aStepTreeNodeHeight + 1
      );
    }
  };

  // This is called to navigate the selected node with nav buttons
  // Need to make it more efficient logic to get right node
  SelectNextEvent = () => {
    let curr = this.state.selectedNodeKey;
    this.bringStepIntoViewOnDownNavBtn(curr);
    let openedTest = this.props.test;
    let allEvents = [];

    if (openedTest.iteration !== undefined && openedTest.iteration.length > 1) {
      openedTest.iteration.forEach(iter => {
        allEvents.push(iter);
        this.putAllEventsToArr(iter.events, allEvents);
      });
    } else if (
      openedTest.iteration !== undefined &&
      openedTest.iteration.length === 1
    ) {
      // only 1 iteration
      if (openedTest.iteration[0].dataset !== undefined) {
        // datatset present for a single iteration
        // put iteration node
        allEvents.push(openedTest.iteration[0]);
      }
      // put all events inside the single  iteration
      this.putAllEventsToArr(openedTest.iteration[0].events, allEvents);
    } else {
      // allEvents = this.state.openedTestChild.events;
      this.putAllEventsToArr(openedTest.events, allEvents);
    }

    this.setState({
      clickedStepDetails: allEvents[parseInt(curr)]
    });
    this.setState({ selectedNodeKey: [parseInt(curr) + 1 + ""] });
    this.makeCallAndSetVp(allEvents[parseInt(curr)]);
    this.setState({ totalNodesCount: allEvents.length });
  };

  // This is called to navigate the selected node with nav buttons
  // Need to make it more efficient logic to get right node
  SelectPreviousEvent = e => {
    let curr = this.state.selectedNodeKey;
    this.bringStepIntoViewOnUpNavBtn(curr);
    let openedTest = this.state.test;
    // let tempEvents = [];
    let allEvents = [];
    if (openedTest.iteration !== undefined && openedTest.iteration.length > 1) {
      openedTest.iteration.forEach(iter => {
        allEvents.push(iter);
        this.putAllEventsToArr(iter.events, allEvents);
      });
    } else if (
      openedTest.iteration !== undefined &&
      openedTest.iteration.length === 1
    ) {
      this.putAllEventsToArr(openedTest.iteration[0].events, allEvents);
    } else {
      this.putAllEventsToArr(this.state.test.events, allEvents);
    }
    this.setState({ clickedStepDetails: allEvents[parseInt(curr) - 1 - 1] });
    this.setState({ selectedNodeKey: [parseInt(curr) - 1 + ""] });
    this.makeCallAndSetVp(allEvents[parseInt(curr) - 1 - 1]);
  };

  // Utitlity method to put the events present in "event" into "allEvents"
  putAllEventsToArr(event, allEvents) {
    event.forEach(ev => {
      if (ev.callScript === undefined) {
        allEvents.push(ev);
      } else {
        if (
          ev.callScript.iteration.length === 1 &&
          ev.callScript.iteration[0].dataset === undefined
        ) {
          allEvents.push(ev);
          this.putAllEventsToArr(ev.callScript.iteration[0].events, allEvents);
        } else {
          allEvents.push(ev);
          ev.callScript.iteration.forEach(iter => {
            allEvents.push(iter);
            this.putAllEventsToArr(iter.events, allEvents);
          });
        }
      }
    });
  }

  renderVirtualScrollForStepTree = () => {
    return (
      this.props.test.summary.totalSteps > _stepsVirtualTreeCount &&
      !this.props.summaryData.executionFinished
    );
  };

  render() {
    const params = new URLSearchParams(window.location.search);
    const xMoebReportPath = params.get(_pathVar);
    let snapShotUri = "";

    // Setting empty image if the iteration itself is not present OR
    // if the events are not present, then show empty component when opened the tab
    if (
      this.props.test.iteration === undefined ||
      (isNotUndefinedAndNull(this.props.test.iteration) &&
        this.props.test.iteration[0].events.length === 0)
    )
      return <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />;
    if (
      this.state.clickedStepDetails !== undefined &&
      this.state.clickedStepDetails.snapshot !== undefined
    ) {
      snapShotUri =
        window.location.origin +
        _reportServiceURIForSnapshotData +
        xMoebReportPath +
        _slashFwd +
        _files +
        _slashFwd +
        window.btoa(this.state.clickedStepDetails.snapshot);
      // only for export option
      if (
        process.env.REACT_APP_EXPORT_MODE === _exportModeYes &&
        process.env.NODE_ENV === "production"
      ) {
        // no need to go through btoa() for this
        snapShotUri =
          _dot +
          _slashFwd +
          _staticDir +
          _slashFwd +
          _mediaDir +
          _slashFwd +
          this.state.clickedStepDetails.snapshot;
      }
    }

    // Used while development
    // snapShotUri = "https://picsum.photos/1080/1731";
    // snapShotUri = "https://picsum.photos/1295/843";

    return (
      <div>
        <div className="test-tab-contents">
          <span
            className={
              this.renderVirtualScrollForStepTree()
                ? "tree-col-execution-not-finished"
                : "tree-col"
            }
            ref={this.stepstreeSpan}
            // span={8}
          >
            {/* Place for Left side tree containing steps */}
            {this.getStepsTree(this.props.test)}
          </span>
          <span className="step-info-section">
            {/* Place for Right side content */}
            <Row
              type="flex"
              justify="space-around"
              align="middle"
              style={{ minHeight: "18%" }}
            >
              <Col span={20}>
                {/* PLace for step Full title, warning etc */}
                {this.getStepDescriptionWithStatus()}
              </Col>
              <Col span={4}>
                <Divider type="vertical" className="divider-stepdesc-navs" />
                {/* Place for navigation btn */}
                <span className="nav-btn-pareent-span">
                  {this.getTestTabNavButtons()}
                </span>
              </Col>
            </Row>
            <Row>
              <Divider className="divider-stepdesc-table" />
            </Row>
            <Row>
              <Col span={17}>
                {/* Place for details of step in table */}
                {this.getStepInfoTable()}
              </Col>
              <Col span={1}>
                <Divider type="vertical" className="divider-stepstbl-scrshot" />
              </Col>

              <Col
                style={{
                  height: "100%"
                }}
                span={6}
              >
                {/* Place for putting image */}
                {this.state.visibleSnapshot &&
                  this.getSnapshotDialog(snapShotUri)}
                {this.getSnapshotInTab(snapShotUri)}
              </Col>
            </Row>
          </span>
        </div>
      </div>
    );
  }
}

/**
 * To encode a url as base64.
 * Detailas: https://stackoverflow.com/questions/11449577/why-is-base64-encode-adding-a-slash-in-the-result
 */
function base64url_encode($s) {
  return window
    .btoa(unescape(encodeURIComponent($s)))
    .replace("/", "_")
    .replace("+", "-");
}

export default TestTabContents;
