import React, { Component } from "react";
import { Table } from "antd";
import {
  _failColorRed,
  _space,
  _colon,
  _vpFullDispStr,
  _textDispStr,
  isNotUndefinedAndNull,
  getVpTableScrollY
} from "./Constants";
import i18n from "../i18n";

const { Column } = Table;

class TextVp extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return this.getTextVpTable(this.props.clickedStepVpContent);
  }

  // Gets Text Vp table
  getTextVpTable = vpFileContent => {
    
    let isVpPass = true;
    let temp = vpFileContent;
    if (temp === undefined) return;

    let data = [];
    let item = { expected: "", actual: "" };
    isNotUndefinedAndNull(temp) &&
      isNotUndefinedAndNull(temp.diff) &&
      temp.diff.length > 0 &&
      temp.diff.forEach(dif => {
        if (dif.match === true) {
          item.expected = (
            <span>
              <span>{item.expected}</span>
              <span>{dif.expected}</span>
              {_space}
            </span>
          );
          item.actual = (
            <span>
              <span>{item.actual}</span>
              <span>{dif.actual}</span>
              {_space}
            </span>
          );
        } else {
          isVpPass = false;
          item.expected = (
            <span>
              <span>{item.expected}</span>
              <span style={{ color: _failColorRed }}>{dif.expected}</span>
              {_space}
            </span>
          );
          item.actual = (
            <span>
              <span>{item.actual}</span>
              <span style={{ color: _failColorRed }}>{dif.actual}</span>
              {_space}
            </span>
          );
        }
      });
    item.key = item.expected + item.actual;
    data.push(item);
    let vptypeDispStr =
      _textDispStr + _space + _vpFullDispStr + _colon + _space;
    let vpResultDispStr = isVpPass
      ? i18n.t("_vpDataAsExpDispStr")
      : i18n.t("_vpDataNotAsExpDispStr");

    return (
      <React.Fragment>
        <div style={{ marginLeft: "15px", marginBottom: "5px" }}>
          <span style={{ fontWeight: "bold" }}>{vptypeDispStr}</span>
          <span>{vpResultDispStr}</span>
        </div>
        <Table
          style={{
            marginLeft: "15px"
          }}
          dataSource={data}
          size="small"
          bordered
          scroll={{ y: getVpTableScrollY() }}
          pagination={false}
          className="ant-table-thead antd-table-row-hover-unset"
        >
          <Column
            className="details-table-row"
            title={
              isVpPass
                ? i18n.t("_expectedDispStr") +
                  _space +
                  i18n.t("_andDispStr") +
                  _space +
                  i18n.t("_actualDispStr")
                : i18n.t("_expectedDispStr")
            }
            dataIndex="expected"
            key="expectedTextVp"
            // width={210}
          />
          {/* enable the column with actual data only when its fail */}
          {isVpPass === false && (
            <Column
              className="details-table-row"
              title={i18n.t("_actualDispStr")}
              dataIndex="actual"
              key="actualTextVp"
            />
          )}
        </Table>
      </React.Fragment>
    );
  };
}

export default TextVp;
