import React from "react";
import Icon from "@ant-design/icons";
import {
  _autTypeWebInJSon,
  _autTypeAppiumAndroidInJSon,
  _autTypeWindowsInJSon,
  _reportServiceURIForSnapshotData,
  _files,
  _dot,
  _staticDir,
  _mediaDir,
  _slashFwd,
  _pathVar,
  _exportModeYes
} from "./Constants";
import { nonWebAutSvg } from "./AllSvg";
import {
  GlobalOutlined,
  WindowsOutlined,
  AndroidOutlined
} from "@ant-design/icons";

// Common method to return icon for Auts; used in
// summary tab, Aut overlay window and in Test tab info icon
const getAutIconForType = (type, autStyle) => {
  let autTypeIconStyle = autStyle;
  switch (type) {
    case _autTypeWebInJSon:
      return <GlobalOutlined style={autTypeIconStyle} />;
    case _autTypeWindowsInJSon:
      return <WindowsOutlined style={autTypeIconStyle} />;
    case _autTypeAppiumAndroidInJSon:
      return <AndroidOutlined style={autTypeIconStyle} />;
    default:
      return (
        <Icon
          aria-label="Any AUT Type"
          alt="AnyAutTypeIcon"
          style={autTypeIconStyle}
          component={nonWebAutSvg}
        ></Icon>
      );
  }
};

// This menthod is used to render the AUT image(specific AUT like website logo captured)
const getAutImg = imgName => {
  const params = new URLSearchParams(window.location.search);
  const xMoebReportPath = params.get(_pathVar);

  let autImgUrl =
    window.location.origin +
    _reportServiceURIForSnapshotData +
    xMoebReportPath +
    _slashFwd +
    _files +
    _slashFwd +
    window.btoa(imgName);
  // only for export option
  if (
    process.env.REACT_APP_EXPORT_MODE === _exportModeYes &&
    process.env.NODE_ENV === "production"
  ) {
    // no need to go through btoa() for this
    autImgUrl =
      _dot +
      _slashFwd +
      _staticDir +
      _slashFwd +
      _mediaDir +
      _slashFwd +
      imgName;
  }

  return (
    <img
      className="aut-logo-img-modal-also"
      alt={"Aut " + imgName}
      src={autImgUrl}
      // For Development only
      // src="https://picsum.photos/200"
    />
  );
};

export { getAutIconForType, getAutImg };
