import React, { Component } from "react";
import { Button, notification, Tooltip } from "antd";
import axios from "axios";
import {
  _space,
  _colon,
  _navigateToTestStepUri,
  _ampersand,
  _stepLocator,
  _equals,
  _slashFwd,
  shouldRenderNavigateToTestOption,
  _projNameKey,
  _scriptNameKey,
  _lineNumKey,
  _2MinsInMillis
} from "./Constants";
import { EditOutlined } from "@ant-design/icons";
import i18n from "../i18n";

class NavigateToTest extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  // static var to track the last time the info notification was shown,
  //so that we can avoid the showing of this message with every click
  //and show only after certain interval
  static lastInfoShownTime = 0;

  render() {
    let ev = this.props.event;

    if (shouldRenderNavigateToTestOption(ev, this.props.test)) {
      return (
        <Tooltip
          trigger={["focus", "hover"]}
          title={i18n.t("_navigateBtnTooltip")}
          placement="topLeft"
          arrowPointAtCenter
        >
          <Button
            shape="circle"
            size="small"
            icon={<EditOutlined />}
            value="buttonWithEditIcon"
            aria-label="buttonWithEditIcon"
            role="button"
            style={{ border: "none", background: "transparent" }}
            onClick={e => this.highlightStepsBtnClick(e, ev)}
          />
        </Tooltip>
      );
    }
    // else render nothing
    return <React.Fragment></React.Fragment>;
  }

  highlightStepsBtnClick = (e, ev) => {
    // Getting the focus out of the Navigation button with Icon.
    // This is because if the user click to navigate and then switches
    // to workbench to see the step/test. Then comes back to report, if
    // the focus is still on the Icon Button, the tooltip pops up(which is incorrect)
    // as we have implemented the tooltip to show on Focus(using TAB key)
    // also for Accessibility.
    if (e && e.currentTarget && e.currentTarget.blur) e.currentTarget.blur();

    // Stop Propogation to prevent click for a Test i.e. when ev=== undefined;
    // but when ev is NOT undefined i.e. the click has happened on the for
    // a step, then don't propogate
    if (ev === undefined) {
      e.stopPropagation();
    }

    let custHeaders = {
      // empty for all other browser but not IE
    };

    if (window.navigator && window.navigator.msSaveOrOpenBlob) {
      // IE only
      custHeaders["Pragma"] = "no-cache";
    }

    axios({
      url:
        //for debug
        // "http://localhost:7080" +
        window.location.origin +
        _navigateToTestStepUri +
        this.getNavigateToStepParams(ev),
      method: "GET",
      headers: custHeaders
    })
      .then(response => {
        let currtime = Date.now();
        // This ensures that the notification doesn't show for subsequent
        //click to nav icon for interval mentioned in the condition
        if (currtime - NavigateToTest.lastInfoShownTime > _2MinsInMillis) {
          NavigateToTest.lastInfoShownTime = Date.now();
          notification.info({
            message: i18n.t("_navigateToWorkbenchInfoNotification"),
            duration: 4
          });
        }
      })
      .catch(error => {
        notification.error({
          message:
            i18n.t("_errorDuringLinkSteps") + _colon + _space + error.message,
          duration: 4
        });
      });
  };

  // Prepare params for Naviagte option REST call.
  getNavigateToStepParams = ev => {
    let testPath = this.props.test.testLocation;
    // event is undefined, meaning the click has been done for a test
    if (ev === undefined) return testPath;

    // Get the line Number property if present in event. If its present
    // then this is a Traditional FT test/event. So prepare the params
    // using lineNum, Projectname and ScriptName
    let lineNumpropObj = undefined;
    if (
      ev.additionalDetails !== undefined &&
      ev.additionalDetails.properties !== undefined
    ) {
      // using filter()[0], instead of find() beacuse its not supported by IE
      lineNumpropObj = ev.additionalDetails.properties.filter(
        prop => prop.key === _lineNumKey
      );

      // For the event passed, get the FIRST event that belongs to this
      // Iteration to get the details of the ProjectName and ScriptName.
      // This is imp for Callscript kind of usecase.
      // We do this by passing the required event and the main Iteration
      //obj as incase of callscript there could be many nested Iteration.
      let iterForEvent = this.getIterationEventForEvent(
        ev,
        this.props.test.iteration
      );
      let thisIterZerothEvent = iterForEvent.events[0];
      let projNameProp = undefined;
      let projValue = undefined;
      let scritpNameProp = undefined;
      let scriptnameValue = undefined;
      if (lineNumpropObj !== undefined && lineNumpropObj.length >= 1) {
        // read Project Name; using filter()[0], instead of find() beacuse its not supported by IE
        projNameProp = thisIterZerothEvent.additionalDetails.properties.filter(
          prop => prop.key === _projNameKey
        );
        if (projNameProp !== undefined && projNameProp.length >= 1) {
          projValue = projNameProp[0].value;
        }
        // read Script Name; read Project Name; using filter()[0], instead of find() beacuse its not supported by IE
        scritpNameProp = thisIterZerothEvent.additionalDetails.properties.filter(
          prop => prop.key === _scriptNameKey
        );
        if (scritpNameProp !== undefined && scritpNameProp.length >= 1) {
          scriptnameValue = scritpNameProp[0].value;
        }

        // navigate usin lineNum; used for traditional ft test
        return (
          projValue +
          _slashFwd +
          scriptnameValue +
          _ampersand +
          _stepLocator +
          _equals +
          lineNumpropObj[0].value
        );
      }
    }

    // Otherwise its a WebUI test/event, use the eventUid for params in REST call
    return testPath + _ampersand + _stepLocator + _equals + ev.eventUid;
  };

  // Returns the complete iteration obj that the passed event belongs to.
  // Along with event, we also pass the main iteration obj initially as
  // this is a recursive call the event could belong to any iteration
  // object as in case of callscript there could be many iteration obj
  getIterationEventForEvent = (evPassed, iterPassed) => {
    for (let i = 0; i < iterPassed.length; i++) {
      let iter = iterPassed[i];
      for (let j = 0; j < iter.events.length; j++) {
        let event = iter.events[j];
        if (event.callScript !== undefined) {
          for (let k = 0; k < event.callScript.iteration.length; k++) {
            let it = this.getIterationEventForEvent(
              evPassed,
              event.callScript.iteration
            );
            if (it !== undefined) return it;
          }
        }
        if (event.eventUid === evPassed.eventUid) {
          return iter;
        }
      }
    }
  };
}

export default NavigateToTest;
