import React, { Component } from "react";
import { Tabs } from "antd";
import { Modal, Button } from "antd";
import { _passColorGreen, _failColorRed, _space, _comma } from "./Constants";
import { getAutIconForType, getAutImg } from "./JsxUtils";
import i18n from "../i18n";

const { TabPane } = Tabs;
class SummaryTabsSection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showAutOverlay: false,
      autListArr: []
    };
  }

  // Modal open up on clicking the AUTs list item in Summary Tab
  openAutModal = () => {
    const autTypeIconStyleInModal = {
      fontSize: "19px",
      marginRight: "5px",
      verticalAlign: "middle"
    };
    return (
      <div>
        <Modal
          role="dialog"
          className="aut-modal"
          title={
            this.state.autListArr.valuesArr.length > 1
              ? i18n.t("_envAutsDispStr")
              : i18n.t("_envAutDispStr")
          }
          visible={this.state.showAutOverlay}
          onOk={this.handleAUTOverlayOk}
          onCancel={this.handleAUTOverlayCancel}
          footer={[
            <Button
              className="footer-btn"
              key="submit"
              type="primary"
              onClick={this.handleAUTOverlayOk}
            >
              {i18n.t("_okDispStr")}
            </Button>
          ]}
        >
          {this.state.autListArr.valuesArr.map((aut, index) => {
            return (
              <p
                key={index}
                className="aut-items"
                style={this.getLiStyle(index)}
              >
                {this.state.autListArr.valuesIconArr[index]
                  ? getAutImg(this.state.autListArr.valuesIconArr[index])
                  : getAutIconForType(
                      this.state.autListArr.valuesTypeArr[index],
                      autTypeIconStyleInModal
                    )}
                {aut}
              </p>
            );
          })}
        </Modal>
      </div>
    );
  };

  render() {
    let itemIndex = -1;
    let tab1Data = this.props.tab1ListData;
    let tab2Data = this.props.tab2ListData;

    const autTypeIconStyle = {
      fontSize: "18px",
      marginRight: "3px",
      verticalAlign: "sub"
    };
    return (
      <Tabs defaultActiveKey="summ-tab" animated>
        <TabPane
          tab={<span className="summ-tab">{i18n.t("_summaryDispStr")}</span>}
          key="summ-tab"
        >
          {/* display list */}
          <ul className="summary-tabs-ul">
            {tab1Data.map(item => {
              itemIndex++;
              return (
                <li
                  key={itemIndex}
                  className="summary-tabs-ul-li"
                  style={this.getLiStyle(itemIndex)}
                >
                  <span>{item.icon}</span>
                  <span className="summary-tabs-ul-li-key">{item.key}</span>
                  <span
                    role="button"
                    // tabIndex set only for AUT values to highlight and click for accessibility
                    tabIndex={item.valuesArr !== undefined ? "0" : "-1"}
                    className="summary-tabs-ul-li-ellipsis"
                    onClick={e => {
                      // Condition to make Click work only for AUT to open the Modal
                      // with AUT details. The "valuesArr" is only part of AUTs obj
                      if (item.valuesArr !== undefined)
                        this.openAUTOverlay(item);
                    }}
                    // onKeyDown event used for keyboardevent
                    onKeyDown={e => {
                      // Condition to make ENTER key work only for AUT to open the Modal
                      // with AUT details. The "valuesArr" is only part of AUTs obj.
                      // Also checking it works for Enter Key i,e (keyCode- 13)
                      if (item.valuesArr !== undefined && e.keyCode === 13)
                        this.openAUTOverlay(item);
                    }}
                  >
                    {/* checking for "valuesArr" as this is only for AUTs as we need to check for AUTType before we render the icon */}
                    {item.valuesArr !== undefined
                      ? item.valuesArr.map((itm, index) => {
                          return (
                            <span key={itm + index}>
                              {item.valuesIconArr[index]
                                ? getAutImg(item.valuesIconArr[index])
                                : getAutIconForType(
                                    item.valuesTypeArr[index],
                                    autTypeIconStyle
                                  )}
                              <span>
                                {itm}
                                {/* Done show comma after the last aut */}
                                {index < item.valuesArr.length - 1 &&
                                  _comma + _space}
                              </span>
                            </span>
                          );
                        })
                      : item.value}
                  </span>
                </li>
              );
            })}
          </ul>
          <br />
          <div className="summary-tabs-tests-lbl">
            {i18n.t("_testsDispStr")}
          </div>
          {this.getTestCounterDiv(
            i18n.t("_totalDispStr") + _space,
            "#0578c3",
            this.props.counteData[0] + this.props.counteData[1]
          )}
          {this.getTestCounterDiv(
            i18n.t("_passedDispStr1") + _space,
            _passColorGreen,
            this.props.counteData[0]
          )}
          {this.getTestCounterDiv(
            i18n.t("_failedDispStr1") + _space,
            _failColorRed,
            this.props.counteData[1]
          )}
          {/* at the end of rendering this tab, open the modal so all other contents are visible */}
          {this.state.showAutOverlay && this.openAutModal()}
        </TabPane>
        <TabPane
          tab={<span className="res-tab">{i18n.t("_resourceDispStr")}</span>}
          key="res-tab"
        >
          {/* display Tab2 list */}
          <ul className="summary-tabs-ul">
            {tab2Data.map(item => {
              itemIndex++;
              return (
                <li
                  key={itemIndex}
                  className="summary-tabs-ul-li"
                  style={this.getLiStyle(itemIndex)}
                >
                  {item.icon}
                  <span className="summary-tabs-ul-li-key">{item.key}</span>
                  <span className="summary-tabs-ul-li-val">{item.value}</span>
                </li>
              );
            })}
          </ul>
        </TabPane>
        {/* <TabPane tab="Tab 3" key="3">
          Content of Tab Pane 3
        </TabPane> */}
      </Tabs>
    );
  }

  openAUTOverlay(autList) {
    this.setState({ showAutOverlay: true });
    this.setState({ autListArr: autList });
  }

  handleAUTOverlayOk = e => {
    this.setState({
      showAutOverlay: false
    });
  };

  handleAUTOverlayCancel = e => {
    this.setState({
      showAutOverlay: false
    });
  };

  getTestCounterDiv(title, colorLeftBorder, value) {
    return (
      <div className="summary-test-counters1">
        <span>{title}</span>
        <div
          className="summary-test-counter-val"
          style={{
            color: colorLeftBorder
          }}
        >
          {value}
        </div>
      </div>
    );
  }

  getLiStyle(itemIndex) {
    return {
      backgroundColor: itemIndex % 2 === 0 ? "white" : "#EFEFEF"
      //   borderBottom: "1px solid #EFEFEF"
    };
  }
}

export default SummaryTabsSection;
