import React, { Component } from "react";
import TestTabContents from "./TestTabContents";
import ReactHtmlParser from "react-html-parser";
import { compTestIconSvg, singleWebUISvg } from "./AllSvg";
import chromeLogo from "../resources/images/chrome.png";
import firefoxLogo from "../resources/images/firefox.png";
import edgeLogo from "../resources/images/edge.png";
import operaLogo from "../resources/images/opera.png";
import ieLogo from "../resources/images/ie.png";
import safariLogo from "../resources/images/safari.png";
import infoLogo from "../resources/images/info.png";

import {
  _failColorRed,
  _passStrInJson,
  _testTypeSingle,
  _testTypeCompound,
  getDuration,
  // for browser icon
  _browserChromeDispStr,
  _browserFirefoxInJson,
  _browserFirefoxDispStr,
  _browserEdgeInJson,
  _browserEdgeDispStr,
  _browserieInJson,
  _browserieDispStr,
  _browserSafariInJson,
  _browserSafariDispStr,
  _browserChromeInJson,
  _pathVar,
  _reportServiceURIForSnapshotData,
  _colon,
  _space,
  _envAutDispStr1,
  _envHostDispStr,
  _testTypeCompoundDispStr,
  _testTypeSingleDispStr,
  _bracesOpen,
  _bracesClose,
  _comma,
  _browserOperaDispStr,
  _browserOperaInJson,
  _slashFwd,
  _files,
  isNotUndefinedAndNull,
  _bracesSqOpen,
  _bracesSqClose,
  _dot,
  _staticDir,
  _mediaDir,
  shouldRenderNavigateToTestOption,
  _exportModeYes,
  _browserEdgeInJson2,
  _browserieInJson2
} from "./Constants";
import Icon from "@ant-design/icons";
import {
  Collapse,
  Button,
  Divider,
  Badge,
  Tooltip,
  Popover,
  Select,
  notification,
  Carousel,
  Modal,
  Checkbox
} from "antd";
import { getAutIconForType, getAutImg } from "./JsxUtils";
import NavigateToTest from "./NavigateToTest";
import {
  InfoCircleOutlined,
  VideoCameraOutlined,
  BarsOutlined,
  DesktopOutlined,
  WindowsOutlined,
  HourglassOutlined
} from "@ant-design/icons";
import i18n from "../i18n";

class TestDetailsSection extends Component {
  static FILTER_VAL_ALL_TESTS = "allTests-1";
  static FILTER_VAL_PASSED_TESTS = "passedTests-1";
  static FILTER_VAL_FAILED_TESTS = "failedTests-1";

  constructor(props) {
    super(props);

    this.state = {
      activeIndex: -1,
      allTests: this.props.testDetailsData[0],
      statusFilterOptions1: [],
      browserFilterOptions1: [],
      checkedFilterPass: false,
      checkedFilterFail: false,
      selectedFilter: [],
      visibleSnapshotVideo: false,
      testForvisibleSnapshotVideo: "",
      selectedFilterTags: [TestDetailsSection.FILTER_VAL_ALL_TESTS]
    };
  }

  componentWillReceiveProps() {
    // update the the allTests state available while live playback depending on the filters selected,
    // so no need to set the state here as the method "filterForSelectedItems" does it
    let filteredItemsArr = this.state.selectedFilter;
    this.filterForSelectedItems(filteredItemsArr);
    // this.setState({ allTests: this.props.testDetailsData[0] });
  }

  onCloseSnapshotVideo = e => {
    this.setState({ visibleSnapshotVideo: false });
  };

  // Moving this style to css has issue when applyging styles for
  // disabled button(example: carousel video button)
  getTestTabBtnStyle() {
    return {
      border: "0px solid black",
      display: "inlineBlock",
      paddingBottom: "4px",
      backgroundColor: "transparent"
    };
  }

  // this returns the items to be rendered on the test tab for a CompTest
  genExtraForCompTest(test) {
    let details = this.getAutAndDeviceDetailsForCompTest(test, this.props);
    // console.log(details);
    return (
      <React.Fragment>
        {test.testSummary !== undefined &&
          test.testSummary.failedTests !== 0 &&
          this.getAvatarAndBadgeForTestTab_FailStepsOrTests(
            <Icon
              aria-label="Single Test Type Icon"
              alt="singleTestTypeIcon"
              component={singleWebUISvg}
              className="failed-test-steps-count-icon"
            />,
            test.testSummary.failedTests,
            i18n.t("_failedTestsDispStr") + _colon + _space,
            _failColorRed
          )}

        <Divider type="vertical" />
        <Popover
          trigger={["focus", "hover"]}
          placement="topLeft"
          arrowPointAtCenter
          content={this.getInfoContent(details, test)}
          title={i18n.t("_testInfoDispStr")}
        >
          <Button
            size="small"
            value="buttonWithTestInfoIcon"
            aria-label="buttonWithTestInfoIcon"
            role="button"
            icon={<InfoCircleOutlined />}
            style={this.getTestTabBtnStyle()}
            onClick={event => {
              // If you don't want click extra trigger collapse, you can prevent this:
              event.stopPropagation();
            }}
          />
        </Popover>
        <Divider type="vertical" />
        {/* getting the first borwser for comptest also as comptest will run in single browser */}
        {/* {this.getIconOrBrowserStr(details.browsers[0].name)} */}

        {/* Showing all the browsers for a compTest */}
        {details.browsers.map(browser => {
          return this.getIconOrBrowserStr(browser);
        })}
        <Divider type="vertical" />
        <Tooltip
          trigger={["focus", "hover"]}
          title={i18n.t("_screenshotCarouselDispStr")}
          placement="topLeft"
          arrowPointAtCenter
        >
          <Button
            disabled={test.snapshotDisabled}
            size="small"
            icon={<VideoCameraOutlined />}
            value="buttonWithVideoIcon"
            aria-label="buttonWithVideoIcon"
            role="button"
            style={this.getTestTabBtnStyle()}
            onClick={event => {
              // If you don't want click extra trigger collapse, you can prevent this:
              event.stopPropagation();
              this.setState({ visibleSnapshotVideo: true });
              this.setState({ testForvisibleSnapshotVideo: test });
            }}
          />
        </Tooltip>
      </React.Fragment>
    );
  }

  // this returns the items to be rendered on the test tab for a Single Test
  genExtraForSingleTest = test => {
    let details = this.getAutAndDeviceDetailsForSingleTest(test, this.props);
    return (
      <React.Fragment>
        {test.summary.failedSteps !== 0 &&
          this.getAvatarAndBadgeForTestTab_FailStepsOrTests(
            <BarsOutlined className="failed-test-steps-count-icon" />,
            test.summary.failedSteps,
            i18n.t("_failedStepsDispStr") + _colon + _space,
            _failColorRed
          )}
        <Divider type="vertical" />
        <Popover
          trigger={["focus", "hover"]}
          placement="topLeft"
          arrowPointAtCenter
          content={this.getInfoContent(details, test)}
          title={i18n.t("_testInfoDispStr")}
        >
          <Button
            size="small"
            value="buttonWithTestInfoIcon"
            aria-label="buttonWithTestInfoIcon"
            role="button"
            icon={<InfoCircleOutlined />}
            style={this.getTestTabBtnStyle()}
            onClick={event => {
              // If you don't want click extra trigger collapse, you can prevent this:
              event.stopPropagation();
            }}
          />
        </Popover>
        <Divider type="vertical" />
        {/* Showing all the browsers for a singleTest */}
        {details.browsers.length > 0 &&
          details.browsers.map(browser => {
            return this.getIconOrBrowserStr(browser);
          })}
        <Divider type="vertical" />
        <Tooltip
          trigger={["focus", "hover"]}
          title={i18n.t("_screenshotCarouselDispStr")}
          placement="topLeft"
          arrowPointAtCenter
        >
          <Button
            disabled={test.snapshotDisabled}
            size="small"
            value="buttonWithVideoIcon"
            aria-label="buttonWithVideoIcon"
            role="button"
            icon={<VideoCameraOutlined />}
            style={this.getTestTabBtnStyle()}
            onClick={event => {
              // If you don't want click extra trigger collapse, you can prevent this:
              event.stopPropagation();
              this.setState({ visibleSnapshotVideo: true });
              this.setState({ testForvisibleSnapshotVideo: test });
            }}
          />
        </Tooltip>
      </React.Fragment>
    );
  };

  handleCarouselOverlayCancel = e => {
    this.setState({
      visibleSnapshotVideo: false
    });
  };

  getCarouselVideoDialog(testL, xMoebReportPath) {
    testL = this.state.testForvisibleSnapshotVideo;

    let snapShotUriForVideo =
      window.location.origin +
      _reportServiceURIForSnapshotData +
      xMoebReportPath +
      _slashFwd +
      _files +
      _slashFwd;

    const modalBodyStyle = { paddingTop: "8px", paddingBottom: "8px" };

    if (testL.type === _testTypeCompound) {
      return (
        <div>
          <Modal
            width={"65%"}
            className="modal-carousel-or-imgzoom"
            bodyStyle={modalBodyStyle}
            title={
              <span
                className={
                  testL.status === _passStrInJson
                    ? "carousel-header pass-left-border"
                    : "carousel-header fail-left-border"
                }
              >
                {testL.testName}
              </span>
            }
            visible={this.state.visibleSnapshotVideo}
            onCancel={this.handleCarouselOverlayCancel}
            footer={null}
          >
            {notification.info({
              message: i18n.t("_pauseOnHoverMsg"),
              duration: 4
            })}

            <Carousel
              autoplay
              autoplaySpeed={1500}
              pauseOnHover={true}
              pauseOnDotsHover={true}
              effect="fade"
            >
              {testL.children.map(test => {
                return test.iteration.map(iter => {
                  return iter.events.map(eventSnapshot => {
                    if (isNotUndefinedAndNull(eventSnapshot.snapshot))
                      return (
                        <div>
                          <div
                            className={
                              "modal-event-title" +
                              (test.status
                                ? " pass-left-border"
                                : " fail-left-border")
                            }
                          >
                            <span style={{ paddingLeft: "5px" }}>
                              {ReactHtmlParser(test.testName)}
                            </span>
                          </div>
                          <div
                            className={
                              "modal-event-title" +
                              (eventSnapshot.status
                                ? " pass-left-border"
                                : " fail-left-border")
                            }
                          >
                            <span style={{ paddingLeft: "5px" }}>
                              {ReactHtmlParser(eventSnapshot.description)}
                            </span>
                          </div>
                          <img
                            className="carousel-img"
                            src={
                              // if the run as for export(i.e _exportModeYes) then,
                              //this changes the the url w.r.t a DOT to use it form a local directory
                              process.env.REACT_APP_EXPORT_MODE ===
                              _exportModeYes
                                ? _dot +
                                  _slashFwd +
                                  _staticDir +
                                  _slashFwd +
                                  _mediaDir +
                                  _slashFwd +
                                  eventSnapshot.snapshot
                                : snapShotUriForVideo +
                                  window.btoa(eventSnapshot.snapshot)
                            }
                            alt={eventSnapshot.snapshot}
                          ></img>
                          {/* Used while development */}
                          {/* <img
                          style={carouselImagesStyle}
                          // src="https://picsum.photos/1080/1731"
                          // src="https://picsum.photos/1295/843"
                          alt="snapshot"
                        ></img> */}
                        </div>
                      );
                    else return null;
                  });
                });
              })}
            </Carousel>
          </Modal>
        </div>
      );
    }

    return (
      <div>
        <Modal
          width={"65%"}
          className="modal-carousel-or-imgzoom"
          bodyStyle={modalBodyStyle}
          title={
            <span
              className={
                "carousel-header" +
                (testL.status === _passStrInJson
                  ? " pass-left-border"
                  : " fail-left-border")
              }
            >
              {testL.testName}
            </span>
          }
          visible={this.state.visibleSnapshotVideo}
          onCancel={this.handleCarouselOverlayCancel}
          footer={null}
        >
          {notification.info({
            message: i18n.t("_pauseOnHoverMsg"),
            duration: 4
          })}

          <Carousel
            autoplay
            autoplaySpeed={1500}
            pauseOnHover={true}
            pauseOnDotsHover={true}
            effect="fade"
          >
            {testL.iteration.map(iter => {
              return iter.events.map(eventSnapshot => {
                if (isNotUndefinedAndNull(eventSnapshot.snapshot))
                  return (
                    <div>
                      <div
                        className={
                          "modal-event-title" +
                          (eventSnapshot.status
                            ? " pass-left-border"
                            : " fail-left-border")
                        }
                      >
                        <span style={{ paddingLeft: "5px" }}>
                          {ReactHtmlParser(eventSnapshot.description)}
                        </span>
                      </div>
                      <img
                        className="carousel-img"
                        src={
                          // if the run as for export(i.e _exportModeYes) then,
                          //this changes the the url w.r.t a DOT to use it form a local directory
                          process.env.REACT_APP_EXPORT_MODE === _exportModeYes
                            ? _dot +
                              _slashFwd +
                              _staticDir +
                              _slashFwd +
                              _mediaDir +
                              _slashFwd +
                              eventSnapshot.snapshot
                            : snapShotUriForVideo +
                              window.btoa(eventSnapshot.snapshot)
                        }
                        alt={eventSnapshot.snapshot}
                      ></img>
                      {/* Used while development */}
                      {/* <img
                        style={carouselImagesStyle}
                        // src="https://picsum.photos/1080/1731"
                        // src="https://picsum.photos/1295/843"
                        alt="snapshot"
                      ></img> */}
                    </div>
                  );
                else return null;
              });
            })}
          </Carousel>
        </Modal>
      </div>
    );
  }

  onFilterChange = value => {
    let selectedFilterItems = value;
    // If "allTests-1" is the last value in value [], that means "allTests-1"
    // has been selected at the end, so ensure to show all test as filter tag
    if (
      selectedFilterItems.length > 1 &&
      selectedFilterItems[selectedFilterItems.length - 1] ===
        TestDetailsSection.FILTER_VAL_ALL_TESTS
    ) {
      selectedFilterItems = [TestDetailsSection.FILTER_VAL_ALL_TESTS];
    }

    // Remove "allTests-1" from the value []
    if (selectedFilterItems.includes(TestDetailsSection.FILTER_VAL_ALL_TESTS)) {
      const index = selectedFilterItems.findIndex(
        ele => ele === TestDetailsSection.FILTER_VAL_ALL_TESTS
      );
      selectedFilterItems.splice(index, 1);
    }

    // After removal of "allTests-1" from the value []
    // if the length is 1, its either pass/fail, show that
    if (selectedFilterItems.length === 1) {
      this.setState({ selectedFilterTags: selectedFilterItems[0] });
    }

    // After removal of "allTests-1" from the value []
    // if the length is 2 or 0, its either pass & fail both or
    // nothing. For both cases, show all tests
    if (selectedFilterItems.length === 2 || selectedFilterItems.length === 0) {
      this.setState({
        selectedFilterTags: [TestDetailsSection.FILTER_VAL_ALL_TESTS]
      });
      selectedFilterItems = [];
    }
    this.filterForSelectedItems(selectedFilterItems);
  };

  // This method taked the items to filter at test level and sets the
  //state varibale "allTests" depending on filter, also sets the "selectedFilters" state
  filterForSelectedItems = selectedStatus => {
    let data = { "passedTests-1": "pass", "failedTests-1": "fail" };
    let testsL;
    if (selectedStatus.length !== 0) {
      testsL = this.props.testDetailsData[0].children;
      selectedStatus.forEach(statusFilter => {
        testsL = testsL.filter(test => test.status === data[statusFilter]);
      });
      let filteredAllTests = { children: testsL };
      this.setState({ allTests: filteredAllTests });
    } else {
      this.setState({ allTests: this.props.testDetailsData[0] });
    }
    this.setState({ selectedFilter: selectedStatus });
  };

  render() {
    // const { Option } = Select;
    const { Option } = Select;
    const { Panel } = Collapse;

    const params = new URLSearchParams(window.location.search);
    const xMoebReportPath = params.get(_pathVar);

    let tests = this.state.allTests;

    return (
      <div>
        <div className="filter-main-div">
          <span className="test-details-text">{i18n.t("_detailsDispStr")}</span>
          <div
            // id="filter-cont1"
            // arial-label="filter-cont1"
            // aria-owns="filter-cont1"
            // aria-controls="filter-cont1"
            // aria-activedescendant="filter-cont1"
            className="filter-text-and-select-container"
          >
            <div className="filter-text">
              {i18n.t("_filtersDispStr") + _colon}
            </div>

            <Select
              aria-expanded={false}
              aria-label="filter-selection"
              aria-labelledby="filter-cont1"
              // role="presentation"
              showArrow
              // removeIcon={
              //   this.state.selectedFilterTags.includes(
              //     TestDetailsSection.FILTER_VAL_ALL_TESTS
              //   ) ? (
              //     <div></div>
              //   ) : null
              // }
              menuItemSelectedIcon={<div />}
              // autoClearSearchValue
              allowClear={false}
              showSearch={false}
              optionLabelProp="label"
              mode="multiple"
              // maxTagPlaceholder={<div></div>}
              onChange={this.onFilterChange}
              value={
                this.state.selectedFilterTags.length === 0
                  ? [TestDetailsSection.FILTER_VAL_ALL_TESTS]
                  : this.state.selectedFilterTags
              }
              className="filter-selection-component"
            >
              {[
                <Option
                  value={TestDetailsSection.FILTER_VAL_ALL_TESTS}
                  label={i18n.t("_showAllTestsDispStr")}
                  title={i18n.t("_showAllTestsDispStr")}
                  key={TestDetailsSection.FILTER_VAL_ALL_TESTS}
                >
                  <Checkbox
                    disabled={
                      this.state.selectedFilterTags.includes(
                        TestDetailsSection.FILTER_VAL_ALL_TESTS
                      ) && this.state.selectedFilterTags.length === 1
                    }
                    checked={this.state.selectedFilterTags.includes(
                      TestDetailsSection.FILTER_VAL_ALL_TESTS
                    )}
                    onChange={this.onChange}
                  ></Checkbox>
                  <span className="filter-chk-title">
                    {i18n.t("_showAllTestsDispStr")}
                  </span>
                </Option>,
                <Option
                  value={TestDetailsSection.FILTER_VAL_PASSED_TESTS}
                  title={i18n.t("_passedDispStr1")}
                  label={i18n.t("_passedDispStr1")}
                  key={TestDetailsSection.FILTER_VAL_PASSED_TESTS}
                >
                  <Checkbox
                    checked={this.state.selectedFilterTags.includes(
                      TestDetailsSection.FILTER_VAL_PASSED_TESTS
                    )}
                    onChange={this.onChange}
                  ></Checkbox>
                  <span className="filter-chk-title">
                    {i18n.t("_passedDispStr1")}
                  </span>
                </Option>,
                <Option
                  value={TestDetailsSection.FILTER_VAL_FAILED_TESTS}
                  label={i18n.t("_failedDispStr1")}
                  title={i18n.t("_failedDispStr1")}
                  key={TestDetailsSection.FILTER_VAL_FAILED_TESTS}
                >
                  <Checkbox
                    checked={this.state.selectedFilterTags.includes(
                      TestDetailsSection.FILTER_VAL_FAILED_TESTS
                    )}
                    onChange={this.onChange}
                  ></Checkbox>
                  <span className="filter-chk-title">
                    {i18n.t("_failedDispStr1")}
                  </span>
                </Option>
              ]}
            </Select>
          </div>
        </div>
        <Collapse expandIconPosition="left" className="test-tab-collpase">
          {tests.children &&
            tests.children.map(test => {
              if (test.type === _testTypeCompound) {
                return (
                  <Panel
                    extra={this.genExtraForCompTest(test, this.props)}
                    className={
                      "collapse-panel" +
                      (test.status === _passStrInJson
                        ? " pass-left-border"
                        : " fail-left-border")
                    }
                    header={
                      <span>
                        <span>
                          {/* render the navigate option based on condition */}
                          {shouldRenderNavigateToTestOption(
                            undefined,
                            test
                          ) && (
                            <React.Fragment>
                              <NavigateToTest test={test} />
                              <Divider type="vertical"></Divider>
                            </React.Fragment>
                          )}

                          <Tooltip
                            trigger={["focus", "hover"]}
                            arrowPointAtCenter
                            placement="topLeft"
                            title={_testTypeCompoundDispStr}
                          >
                            <Button
                              size="small"
                              value="buttonWithTestTypeIcon"
                              aria-label="buttonWithTestTypeIcon"
                              role="button"
                              onClick={event => {
                                // If you don't want click extra trigger collapse, you can prevent this:
                                event.stopPropagation();
                              }}
                              className="test-type-ico-btn"
                            >
                              <Icon
                                aria-label="Compound Test Type Icon"
                                alt="compTestTypeIcon"
                                // style={testTypeIconStyle}
                                className="test-type-icon"
                                component={compTestIconSvg}
                              />
                            </Button>
                          </Tooltip>
                        </span>
                        <Divider type="vertical"></Divider>
                        <span className="test-tab-name">{test.testName}</span>
                      </span>
                    }
                    key={test.testUId + test.testName}
                  >
                    <Collapse
                      expandIconPosition="left"
                      defaultActiveKey="1"
                      className="test-tab-collpase"
                    >
                      {test.children &&
                        test.children.map(childTest => {
                          return (
                            <Panel
                              extra={this.genExtraForSingleTest(
                                childTest,
                                this.props
                              )}
                              className={
                                "collapse-panel" +
                                (childTest.status === _passStrInJson
                                  ? " pass-left-border"
                                  : " fail-left-border")
                              }
                              header={
                                <span>
                                  {/* render the navigate option based on condition */}
                                  {shouldRenderNavigateToTestOption(
                                    undefined,
                                    childTest
                                  ) && (
                                    <React.Fragment>
                                      <NavigateToTest test={childTest} />
                                      <Divider type="vertical"></Divider>
                                    </React.Fragment>
                                  )}
                                  <span>
                                    <Tooltip
                                      trigger={["focus", "hover"]}
                                      arrowPointAtCenter
                                      placement="topLeft"
                                      title={_testTypeSingleDispStr}
                                    >
                                      <Button
                                        size="small"
                                        value="buttonWithTestTypeIcon"
                                        aria-label="buttonWithTestTypeIcon"
                                        role="button"
                                        onClick={event => {
                                          // If you don't want click extra trigger collapse, you can prevent this:
                                          event.stopPropagation();
                                        }}
                                        className="test-type-ico-btn"
                                      >
                                        <Icon
                                          aria-label="Single Test Type Icon"
                                          alt="singleTestTypeIcon"
                                          component={singleWebUISvg}
                                          className="test-type-icon"
                                        />
                                      </Button>
                                    </Tooltip>
                                  </span>
                                  <Divider type="vertical"></Divider>
                                  <span className="test-tab-name">
                                    {childTest.testName}
                                  </span>
                                </span>
                              }
                              key={childTest.testUId + childTest.testName}
                            >
                              <div>
                                <TestTabContents
                                  // eventsData={test.events}
                                  allTests={tests}
                                  test={childTest}
                                  summaryEnvironment={
                                    this.props.summaryEnvironment
                                  }
                                  summaryData={this.props.summaryData}
                                />
                              </div>
                            </Panel>
                          );
                        })}
                    </Collapse>
                  </Panel>
                );
              }

              if (test.type === _testTypeSingle) {
                return (
                  <Panel
                    extra={this.genExtraForSingleTest(test, this.props)}
                    className={
                      "collapse-panel" +
                      (test.status === _passStrInJson
                        ? " pass-left-border"
                        : " fail-left-border")
                    }
                    header={
                      <span>
                        {/* render the navigate option based on condition */}
                        {shouldRenderNavigateToTestOption(undefined, test) && (
                          <React.Fragment>
                            <NavigateToTest test={test} />
                            <Divider type="vertical"></Divider>
                          </React.Fragment>
                        )}
                        <span>
                          <Tooltip
                            trigger={["focus", "hover"]}
                            arrowPointAtCenter
                            placement="topLeft"
                            title={_testTypeSingleDispStr}
                          >
                            <Button
                              size="small"
                              value="buttonWithTestTypeIcon"
                              aria-label="buttonWithTestTypeIcon"
                              role="button"
                              onClick={event => {
                                // If you don't want click extra trigger collapse, you can prevent this:
                                event.stopPropagation();
                              }}
                              className="test-type-ico-btn"
                            >
                              <Icon
                                aria-label="Single Test Type Icon"
                                alt="singleTestTypeIcon"
                                className="test-type-icon"
                                component={singleWebUISvg}
                              />
                            </Button>
                          </Tooltip>
                        </span>
                        <Divider type="vertical"></Divider>
                        <span className="test-tab-name">{test.testName}</span>
                      </span>
                    }
                    key={test.testUId + test.testName}
                  >
                    <TestTabContents
                      // eventsData={test.events}
                      allTests={tests}
                      test={test}
                      summaryEnvironment={this.props.summaryEnvironment}
                      summaryData={this.props.summaryData}
                    />
                  </Panel>
                );
              } else return null;
            })}
        </Collapse>
        {/* do extra stuff which happen on some condition like carousel video */}
        {this.state.visibleSnapshotVideo &&
          this.getCarouselVideoDialog(
            this.state.testForvisibleSnapshotVideo,
            xMoebReportPath
          )}
      </div>
    );
  }

  getAvatarAndBadgeForTestTab_FailStepsOrTests(
    logo,
    badgeNmber,
    tooltipStr,
    badgeColor
  ) {
    const badgeStyle = {
      backgroundColor: badgeColor,
      marginRight: "5px"
    };
    if (badgeNmber === 0) return;
    return (
      <Tooltip
        title={tooltipStr + badgeNmber}
        trigger={["focus", "hover"]}
        placement="topRight"
        arrowPointAtCenter
      >
        <span
          style={{
            marginRight: "15px"
          }}
        >
          {/* adding space as title avoids badge hover tooltip as we have a tootltip already */}
          <Badge title={_space} count={badgeNmber} showZero style={badgeStyle}>
            <Button
              size="small"
              value="buttonWithFailedStepsIcon"
              aria-label="buttonWithFailedStepsIcon"
              role="button"
              onClick={event => {
                // If you don't want click extra trigger collapse, you can prevent this:
                event.stopPropagation();
              }}
              className="fail-tests-or-steps-ico-btn"
            >
              {logo}
            </Button>
          </Badge>
        </span>
      </Tooltip>
    );
  }

  getIconOrBrowserStr(browserObj) {
    let addtionalTooltip = "";
    let mode = "";
    if (isNotUndefinedAndNull(browserObj.mode) && browserObj.mode !== "") {
      mode = browserObj.mode;
      let arch = "";
      if (
        isNotUndefinedAndNull(browserObj.architecture) &&
        browserObj.architecture !== ""
      ) {
        arch = browserObj.architecture;
      }

      if (arch !== "") {
        addtionalTooltip =
          _space +
          _bracesSqOpen +
          mode +
          _comma +
          _space +
          arch +
          _bracesSqClose;
      } else {
        addtionalTooltip = _space + _bracesSqOpen + mode + _bracesSqClose;
      }
    } 
    if (
      isNotUndefinedAndNull(browserObj.version) &&
      browserObj.version !== ""
    ) {
      addtionalTooltip =
        _space + _bracesOpen + browserObj.version + _bracesClose + _space + addtionalTooltip;
    }

    if (browserObj.name.startsWith(_browserChromeInJson))
      return this.getBrowserIcon(
        chromeLogo,
        _browserChromeDispStr + addtionalTooltip
      );
    else if (browserObj.name.startsWith(_browserFirefoxInJson))
      return this.getBrowserIcon(
        firefoxLogo,
        _browserFirefoxDispStr + addtionalTooltip
      );
    else if (
      browserObj.name.startsWith(_browserEdgeInJson) ||
      browserObj.name.startsWith(_browserEdgeInJson2)
    )
      return this.getBrowserIcon(
        edgeLogo,
        _browserEdgeDispStr + addtionalTooltip
      );
    else if (
      browserObj.name.startsWith(_browserieInJson) ||
      browserObj.name.startsWith(_browserieInJson2)
    )
      return this.getBrowserIcon(ieLogo, _browserieDispStr + addtionalTooltip);
    else if (browserObj.name.startsWith(_browserSafariInJson))
      return this.getBrowserIcon(
        safariLogo,
        _browserSafariDispStr + addtionalTooltip
      );
    else if (browserObj.name.startsWith(_browserOperaInJson))
      return this.getBrowserIcon(
        operaLogo,
        _browserOperaDispStr + addtionalTooltip
      );
    else
      return this.getBrowserIcon(infoLogo, browserObj.name + addtionalTooltip);
  }

  getBrowserIcon(logo, tooltip) {
    return (
      <Tooltip
        key={tooltip + logo}
        title={tooltip}
        trigger={["focus", "hover"]}
        placement="topRight"
        arrowPointAtCenter
      >
        <span>
          <Button
            value="buttonWithLogoImg"
            aria-label="buttonWithLogoImg"
            role="button"
            size="small"
            className="runtime-img-btn"
            onClick={event => {
              // If you don't want click extra trigger collapse, you can prevent this:
              event.stopPropagation();
            }}
          >
            <img
              aria-hidden="false"
              width={20}
              src={logo}
              alt="logo"
              style={{
                borderRadius: "50%"
              }}
            ></img>
          </Button>
        </span>
      </Tooltip>
    );
  }

  // this method goes through all the tests inside a compt test and returns am array of devices
  getAutAndDeviceDetailsForCompTest(test, props) {
    let auts11 = [];
    let browser11 = [];
    let hostInfo11 = [];
    let details11 = {
      auts: auts11,
      browsers: browser11,
      hostInformation: hostInfo11
    };
    for (var i = 0; test.children && i < test.children.length; i++) {
      let d = this.getAutAndDeviceDetailsForSingleTest(test.children[i], props);

      if (
        d.auts.length > 0 &&
        details11.auts.filter(aut => aut.id === d.auts[0].id).length === 0
      )
        details11.auts = [...details11.auts, ...d.auts];

      if (
        d.browsers.length > 0 &&
        details11.browsers.filter(br => br.id === d.browsers[0].id).length === 0
      )
        details11.browsers = [...details11.browsers, ...d.browsers];

      if (
        d.hostInformation.length > 0 &&
        details11.hostInformation.filter(
          host => host.id === d.hostInformation[0].id
        ).length === 0
      )
        details11.hostInformation = [
          ...details11.hostInformation,
          ...d.hostInformation
        ];
    }
    // console.log(details11);

    // remove duplicare indexes
    details11.auts = [...new Set(details11.auts)];
    details11.browsers = [...new Set(details11.browsers)];
    details11.hostInformation = [...new Set(details11.hostInformation)];
    return details11;
  }

  // Revisit
  getAutAndDeviceDetailsForSingleTest(test, props) {
    let autsArr = [];
    let browserArr = [];
    let hostInfoArr = [];
    let detailsArrAutBrowserHost = {};

    detailsArrAutBrowserHost.auts = autsArr;
    detailsArrAutBrowserHost.browsers = browserArr;
    detailsArrAutBrowserHost.hostInformation = hostInfoArr;

    if (
      isNotUndefinedAndNull(test.environmentIndex) &&
      isNotUndefinedAndNull(test.environmentIndex.aut) &&
      test.environmentIndex.aut.length > 0
    ) {
      test.environmentIndex.aut.forEach((aut, i) => {
        detailsArrAutBrowserHost.auts.push(
          props.summaryEnvironment.aut[test.environmentIndex.aut[i]]
        );
      });
    }

    if (
      isNotUndefinedAndNull(test.environmentIndex) &&
      isNotUndefinedAndNull(test.environmentIndex.configuration) &&
      test.environmentIndex.configuration.length > 0
    ) {
      test.environmentIndex.configuration.forEach((config, i) => {
        detailsArrAutBrowserHost.browsers.push(
          props.summaryEnvironment.configuration[
            test.environmentIndex.configuration[i]
          ]
        );
      });
    }

    if (
      isNotUndefinedAndNull(test.environmentIndex) &&
      isNotUndefinedAndNull(test.environmentIndex.platform) &&
      test.environmentIndex.platform.length > 0
    ) {
      test.environmentIndex.platform.forEach((plat, i) => {
        detailsArrAutBrowserHost.hostInformation.push(
          props.summaryEnvironment.platform[test.environmentIndex.platform[i]]
        );
      });
    }

    return detailsArrAutBrowserHost;
  }

  // The content shown in the info button in test tab is calculated here
  getInfoContent(details, test) {
    if (
      details.auts.length === 0 ||
      details.hostInformation === undefined ||
      details.hostInformation.length === 0
    )
      return;
    let hostValueStr = details.hostInformation[0].name;
    if (isNotUndefinedAndNull(details.hostInformation[0].hostIp)) {
      hostValueStr =
        hostValueStr +
        _space +
        _bracesOpen +
        details.hostInformation[0].hostIp +
        _bracesClose;
    }

    // Not changed this to css file as this style obj is sent to
    // methods to get Icons based on diff style
    const infoBtnAutIconStyle = {
      fontSize: "15px",
      marginRight: "5px"
    };
    return (
      <div>
        <p>
          <DesktopOutlined style={infoBtnAutIconStyle} />
          {_envAutDispStr1 + _colon + _space}
          {details.auts.map((aut, index) => {
            return (
              <span key={aut.id} style={infoBtnAutIconStyle}>
                {aut.icon
                  ? getAutImg(aut.icon)
                  : getAutIconForType(aut.type, infoBtnAutIconStyle)}
                <span>
                  {aut.name}
                  {index < details.auts.length - 1 && _comma}
                </span>
              </span>
            );
          })}
        </p>
        <p>
          <WindowsOutlined style={infoBtnAutIconStyle} />
          {_envHostDispStr + _colon + _space + hostValueStr}
        </p>
        <p>
          <HourglassOutlined style={infoBtnAutIconStyle} />
          {i18n.t("_durationDispStr") + _colon}{" "}
          {getDuration(test.executionTime)}
        </p>
      </div>
    );
  }
}

export default TestDetailsSection;
